import React, { useState, useEffect, useContext } from "react";
import { AuthContext } from "../../Auth";
import firebase from "../firebase";
import { Button, Typography, Grid, FormLabel, Paper } from "@material-ui/core";
import "./YourDreams.css";
import down from "../images/down.png";
const SORT_OPTIONS = {
  TimestampAsc: { column: "timestamp", direction: "asc" },
  TimestampDesc: { column: "timestamp", direction: "desc" },
  NameAsc: { column: "Name", direction: "asc" },
  NameDesc: { column: "Name", direction: "desc" },
  LocationAsc: { column: "location", direction: "asc" },
  LocationDesc: { column: "location", direction: "desc" },
  RealityAsc: { column: "RealityValue", direction: "desc" },
  RealityDesc: { column: "RealityValue", direction: "asc" },
  ActiRangeAsc: { column: "ActiRange", direction: "asc" },
  ActiRangeDesc: { column: "ActiRange", direction: "desc" },
};

function useDream(sortBy = "TIME_ASC") {
  const [dreams, setdreams] = useState([]);

  const { currentUser } = useContext(AuthContext);
  useEffect(() => {
    firebase
      .firestore()
      .collection("Users")
      .doc(currentUser.email)
      .collection("dreams")
      .orderBy(SORT_OPTIONS[sortBy].column, SORT_OPTIONS[sortBy].direction)
      .onSnapshot((snapshot) => {
        const newDreams = snapshot.docs.map((doc) => ({
          id: doc.id,
          ...doc.data(),
        }));
        setdreams(newDreams);
      });
  }, [sortBy]);

  return dreams;
}
function YourDreams(props) {
  const [swap, setswap] = useState(false);
  const [sortBy, setSortBy] = useState("TimestampAsc");
  const dreams = useDream(sortBy);
  const changeswap = (dream, index) => {
    return setswap(!swap);

    // return dream.Description.length > 0 ? (
    //   <div>
    //     {console.log(dream.Description)}
    //     <Typography color="secondary" variant="h5">
    //       Description
    //     </Typography>
    //     <Typography color="primary" variant="h6">
    //       {dream.Description}
    //     </Typography>
    //   </div>
    // ) : null;
  };
  return (
    <div className="kingwrap">
      {dreams.length == 0 ? (
        <div className="dreamwrapper">
          <div>
            <Typography variant="h3" color="secondary">
              No dreams to report yet. Report a dream
            </Typography>
            <br />
            <Button
              onClick={props.startdream}
              color="primary"
              variant="contained"
            >
              Report Dream
            </Button>
          </div>
        </div>
      ) : (
        <div className="wrapper">
          <Typography color="primary" variant="h4">
            Your Dream Web
          </Typography>

          <FormLabel color="secondary">Sort By:</FormLabel>
          <br />
          <select
            value={sortBy}
            onChange={(e) => setSortBy(e.currentTarget.value)}
          >
            <option value="TimestampAsc">Time (first)</option>
            <option value="TimestampDesc">Time (recent)</option>
            <option>--</option>
            <option value="NameAsc"> Name A</option>
            <option value="NameDesc">Name Z</option>
            <option>--</option>
            <option value="LocationAsc">Location A</option>
            <option value="LocationDesc">Location Z</option>
            <option>--</option>
            <option value="RealityAsc">Most Real</option>
            <option value="RealityDesc">Least Real</option>
            <option>--</option>
            <option value="ActiRangeAsc">Most Emotionally Intense</option>
            <option value="ActiRangeDesc">Least Emotionally Intense</option>
          </select>
          <Grid container justify="center">
            <Grid item>
              {console.log(swap)}
              <ul className="containera">
                {dreams.map((dream, i) => {
                  return (
                    <Grid container key={i} justify="center">
                      <Grid item>
                        <li className="yourli">
                          <div className="dreamcardwrapper">
                            <Typography color="primary" variant="h3">
                              {dream.Name}
                            </Typography>

                            {swap && dream.Description.length > 0 ? (
                              <div>
                                <Typography color="secondary" variant="h5">
                                  Description
                                </Typography>
                                <Typography color="primary" variant="h6">
                                  {dream.Description}
                                </Typography>
                              </div>
                            ) : null}
                            {swap && dream.Emolist.length > 0 ? (
                              <div>
                                <Typography color="secondary" variant="h5">
                                  Emotions Felt
                                </Typography>
                                <Typography color="primary" variant="h4">
                                  {dream.Emolist.map((emo) => {
                                    return <li>{emo}</li>;
                                  })}
                                </Typography>
                              </div>
                            ) : null}
                            {swap && dream.Character.length > 0 ? (
                              <div>
                                <Typography color="secondary" variant="h5">
                                  Characters
                                </Typography>
                                <Typography color="primary" variant="h5">
                                  {dream.Character.map((ch) => {
                                    return <li>{ch}</li>;
                                  })}
                                </Typography>
                              </div>
                            ) : null}
                            {swap && dream.RealityValue !== "" ? (
                              <div>
                                <Typography color="secondary" variant="h5">
                                  RealityValue
                                </Typography>
                                <Typography color="primary" variant="h4">
                                  {dream.RealityValue}%
                                </Typography>
                              </div>
                            ) : null}
                            {swap && dream.ActiRange !== "" ? (
                              <div>
                                <Typography color="secondary" variant="h5">
                                  Emotion Intensity
                                </Typography>
                                <Typography color="primary" variant="h4">
                                  {dream.ActiRange}%
                                </Typography>
                              </div>
                            ) : null}
                            {swap && dream.Time !== "" ? (
                              <div>
                                <Typography color="secondary" variant="h5">
                                  Time Felt
                                </Typography>
                                <Typography color="primary" variant="h4">
                                  {dream.Time}
                                </Typography>
                              </div>
                            ) : null}
                            {swap && dream.Location !== "" ? (
                              <div>
                                <Typography color="secondary" variant="h5">
                                  Location
                                </Typography>
                                <Typography color="primary" variant="h4">
                                  {dream.location}
                                </Typography>
                              </div>
                            ) : null}
                            <Button
                              onClick={() => changeswap(dream)}
                              color="secondary"
                              variant="contained"
                            >
                              {swap ? (
                                "hide"
                              ) : (
                                <img className="down" src={down} />
                              )}
                            </Button>
                          </div>
                          {/* <div className="dreamcardwrapper">
                            {dream.Name.length > 0 ? (
                              <div>
                                <Typography color="secondary" variant="h4">
                                  Name
                                </Typography>
                                <Typography color="primary" variant="h2">
                                  {dream.Name}
                                </Typography>
                              </div>
                            ) : null}
                            {dream.Description.length > 0 ? (
                              <div>
                                <Typography color="secondary" variant="h5">
                                  Description
                                </Typography>
                                <Typography color="primary" variant="h6">
                                  {dream.Description}
                                </Typography>
                              </div>
                            ) : null}
                            {dream.Emolist.length > 0 ? (
                              <div>
                                <Typography color="secondary" variant="h5">
                                  Emotions Felt
                                </Typography>
                                <Typography color="primary" variant="h4">
                                  {dream.Emolist.map((emo) => {
                                    return <li>{emo}</li>;
                                  })}
                                </Typography>
                              </div>
                            ) : null}
                            {dream.Character.length > 0 ? (
                              <div>
                                <Typography color="secondary" variant="h5">
                                  Characters
                                </Typography>
                                <Typography color="primary" variant="h5">
                                  {dream.Character.map((ch) => {
                                    return <li>{ch}</li>;
                                  })}
                                </Typography>
                              </div>
                            ) : null}
                            {dream.RealityValue !== "" ? (
                              <div>
                                <Typography color="secondary" variant="h5">
                                  RealityValue
                                </Typography>
                                <Typography color="primary" variant="h4">
                                  {dream.RealityValue}%
                                </Typography>
                              </div>
                            ) : null}

                            {dream.ActiRange !== "" ? (
                              <div>
                                <Typography color="secondary" variant="h5">
                                  Emotion Intensity
                                </Typography>
                                <Typography color="primary" variant="h4">
                                  {dream.ActiRange}%
                                </Typography>
                              </div>
                            ) : null}
                            {dream.Time !== "" ? (
                              <div>
                                <Typography color="secondary" variant="h5">
                                  Time Felt
                                </Typography>
                                <Typography color="primary" variant="h4">
                                  {dream.Time}
                                </Typography>
                              </div>
                            ) : null}
                            {dream.Location !== "" ? (
                              <div>
                                <Typography color="secondary" variant="h5">
                                  Location
                                </Typography>
                                <Typography color="primary" variant="h4">
                                  {dream.location}
                                </Typography>
                              </div>
                            ) : null}
                          </div> */}
                        </li>
                      </Grid>
                    </Grid>
                  );
                })}
              </ul>
            </Grid>
          </Grid>
        </div>
      )}
    </div>
  );
}

export default YourDreams;
