import React, { useState, useEffect, useContext } from "react";
import firebase from "../firebase";
import { AuthContext } from "../../Auth";
import { Typography, Box, Divider, Grid, Paper } from "@material-ui/core";
import "./character.css";
import { Bar } from "react-chartjs-2";

function Usercomparison() {
  const [dreamz, setdreamz] = useState([]);
  const [dreams, setdreams] = useState([]);
  const { currentUser } = useContext(AuthContext);
  const [scene, setscene] = useState("bar");

  useEffect(() => {
    const fetchData = async () => {
      const db = firebase.firestore();
      const data = await db.collection("Dream Total").get();
      setdreamz(data.docs.map((doc) => doc.data()));
    };
    fetchData();
  }, []);

  useEffect(() => {
    const fetchData = async () => {
      const db = firebase.firestore();
      const data = await db
        .collection("Users")
        .doc(currentUser.email)
        .collection("dreams")
        .get();
      setdreams(data.docs.map((doc) => doc.data()));
    };
    fetchData();
  }, []);

  const charactertotal = dreamz.map((dream) => {
    return dream.Character;
  });
  const yourcharacterlist = dreams.map((dr) => {
    return dr.Character;
  });

  const countOccurrences = function (arr, val) {
    return arr.reduce((acc, elem) => {
      return val === elem ? acc + 1 : acc;
    }, 0);
  };
  const mergechartotal = [].concat.apply([], charactertotal);
  const yourchartotal = [].concat.apply([], yourcharacterlist);
  console.log(yourchartotal);

  //personal occurance array
  const Friends = countOccurrences(yourchartotal, "Friends");
  const Monsters = countOccurrences(yourchartotal, "Monsters");
  const NoPeople = countOccurrences(yourchartotal, "No People");
  const Family = countOccurrences(yourchartotal, "Family");

  const Celebrities = countOccurrences(yourchartotal, "Celebrities");
  const Strangers = countOccurrences(yourchartotal, "Strangers");
  const LookedFamiliar = countOccurrences(yourchartotal, "Looked Familiar");
  const Peoplefrommypast = countOccurrences(
    yourchartotal,
    "People from my Past"
  );
  const Animals = countOccurrences(yourchartotal, "Animals");

  // global occurance array
  const globalFriends = countOccurrences(mergechartotal, "Friends");
  const globalMonsters = countOccurrences(mergechartotal, "Monsters");
  const globalNoPeople = countOccurrences(mergechartotal, "No People");

  const globalFamily = countOccurrences(mergechartotal, "Family");

  const globalCelebrities = countOccurrences(mergechartotal, "Celebrities");
  const globalStrangers = countOccurrences(mergechartotal, "Strangers");
  const globalLookedFamiliar = countOccurrences(
    mergechartotal,
    "Looked Familiar"
  );
  const globalPeoplefrommypast = countOccurrences(
    mergechartotal,
    "People from my Past"
  );
  const globalAnimals = countOccurrences(mergechartotal, "Animals");
  //  personal occurrence array
  const globalcharacterarray = [
    globalFriends,
    globalMonsters,
    globalNoPeople,
    globalFamily,
    globalCelebrities,
    globalStrangers,
    globalLookedFamiliar,
    globalPeoplefrommypast,
    globalAnimals,
  ];
  const yourcharacterarray = [
    Friends,
    Monsters,
    NoPeople,
    Family,
    Celebrities,
    Strangers,
    LookedFamiliar,
    Peoplefrommypast,
    Animals,
  ];
  // add array
  const arrSum = (arr) => arr.reduce((a, b) => a + b, 0);

  const globalsum = arrSum(globalcharacterarray);

  const yoursum = arrSum(yourcharacterarray);

  //global averages
  const globalfriendaverage = Math.round((globalFriends / globalsum) * 100);
  const globalmonstersaverage = Math.round((globalMonsters / globalsum) * 100);
  const globalNoPeopleaverage = Math.round((globalNoPeople / globalsum) * 100);
  const globalFamilyaverage = Math.round((globalFamily / globalsum) * 100);
  const globalCelebritiesaverage = Math.round(
    (globalCelebrities / globalsum) * 100
  );

  const globalStrangersaverage = Math.round(
    (globalStrangers / globalsum) * 100
  );

  const globalLookedFamiliaraverage = Math.round(
    (globalLookedFamiliar / globalsum) * 100
  );
  const globalPeoplefrommypastaverage = Math.round(
    (globalPeoplefrommypast / globalsum) * 100
  );

  const globalAnimalsaverage = Math.round((globalAnimals / globalsum) * 100);
  // your averages
  const yourfriendaverage = Math.round((Friends / yoursum) * 100);
  const yourMonsteraverage = Math.round((Monsters / yoursum) * 100);
  const yourNoPeopleaverage = Math.round((NoPeople / yoursum) * 100);
  console.log(yourNoPeopleaverage);
  const yourFamilyaverage = Math.round((Family / yoursum) * 100);
  const yourCelebritiesaverage = Math.round((Celebrities / yoursum) * 100);
  const yourStrangersaverage = Math.round((Strangers / yoursum) * 100);
  const yourLookedFamiliaraverage = Math.round(
    (LookedFamiliar / yoursum) * 100
  );
  const yourPeoplefrommypastaverage = Math.round(
    (Peoplefrommypast / yoursum) * 100
  );
  const yourAnimalsaverage = Math.round((Animals / yoursum) * 100);

  const frienddifference = (globalfriendaverage - yourfriendaverage) * -1;
  const monsterdifference = (globalmonstersaverage - yourMonsteraverage) * -1;
  const NoPeopledifference = (globalNoPeopleaverage - yourNoPeopleaverage) * -1;
  const Familydifference = (globalFamilyaverage - yourFamilyaverage) * -1;
  const Celebritiesdifference =
    (globalCelebritiesaverage - yourCelebritiesaverage) * -1;
  const Strangerdifference =
    (globalStrangersaverage - yourStrangersaverage) * -1;
  const LookedFamiliardifference =
    (globalLookedFamiliaraverage - yourLookedFamiliaraverage) * -1;
  const peoplefrommypastdifference =
    (globalPeoplefrommypastaverage - yourPeoplefrommypastaverage) * -1;
  const animalsdifference = Math.round(
    (globalAnimalsaverage - yourAnimalsaverage) * -1
  );

  const characterdiffarray = [
    frienddifference,
    monsterdifference,
    NoPeopledifference,
    Familydifference,
    Celebritiesdifference,
    peoplefrommypastdifference,
    Strangerdifference,
    LookedFamiliardifference,
    animalsdifference,
  ];
  let friendswords = "";

  if (frienddifference > 0) {
    friendswords = (
      <Typography color="secondary">
        {Math.abs(frienddifference)}% more friend dreams
      </Typography>
    );
  } else if (frienddifference == 0) {
    friendswords = (
      <Typography color="secondary">average amount of friend dreams</Typography>
    );
  } else if (frienddifference < 0) {
    friendswords = (
      <Typography color="secondary">
        {Math.abs(frienddifference)}% less fear dreams
      </Typography>
    );
  }
  let monsterwords = "";
  if (monsterdifference > 0) {
    monsterwords = (
      <Typography color="secondary">
        {Math.abs(monsterdifference)}% more monster dreams
      </Typography>
    );
  } else if (monsterdifference == 0) {
    monsterwords = (
      <Typography color="secondary">
        average amount of monster dreams
      </Typography>
    );
  } else if (monsterdifference < 0) {
    monsterwords = (
      <Typography color="secondary">
        {Math.abs(monsterdifference)}% less no people dreams
      </Typography>
    );
  }
  let nopeoplewords = "";
  if (NoPeopledifference > 0) {
    nopeoplewords = (
      <Typography color="secondary">
        {Math.abs(NoPeopledifference)}% more no people dreams
      </Typography>
    );
  } else if (NoPeopledifference == 0) {
    nopeoplewords = (
      <Typography color="secondary">
        average amount of no people dreams
      </Typography>
    );
  } else if (NoPeopledifference < 0) {
    nopeoplewords = (
      <Typography color="secondary">
        {Math.abs(NoPeopledifference)}% less no people dreams
      </Typography>
    );
  }

  if (frienddifference > 0) {
    friendswords = (
      <Typography color="secondary">
        {Math.abs(frienddifference)}% more friend dreams
      </Typography>
    );
  } else if (frienddifference == 0) {
    friendswords = (
      <Typography color="secondary">average amount of friend dreams</Typography>
    );
  } else if (frienddifference < 0) {
    friendswords = (
      <Typography color="secondary">
        {Math.abs(frienddifference)}% less friend dreams
      </Typography>
    );
  }
  let celebritywords = "";
  if (Celebritiesdifference > 0) {
    celebritywords = (
      <Typography color="secondary">
        {Math.abs(Celebritiesdifference)}% more celebrity dreams
      </Typography>
    );
  } else if (Celebritiesdifference == 0) {
    celebritywords = (
      <Typography color="secondary">
        average amount of celebrity dreams
      </Typography>
    );
  } else if (Celebritiesdifference < 0) {
    celebritywords = (
      <Typography color="secondary">
        {Math.abs(Celebritiesdifference)}% less celebrity dreams
      </Typography>
    );
  }
  let Strangerwords = "";
  if (Strangerdifference > 0) {
    Strangerwords = (
      <Typography color="secondary">
        {Math.abs(Strangerdifference)}% more stranger dreams
      </Typography>
    );
  } else if (Strangerdifference == 0) {
    Strangerwords = (
      <Typography color="secondary">
        average amount of stranger dreams
      </Typography>
    );
  } else if (Strangerdifference < 0) {
    Strangerwords = (
      <Typography color="secondary">
        {Math.abs(Strangerdifference)}% less stranger dreams
      </Typography>
    );
  }

  let familiarwords = "";
  if (LookedFamiliardifference > 0) {
    familiarwords = (
      <Typography color="secondary">
        {Math.abs(LookedFamiliardifference)}% more looked familiar dreams
      </Typography>
    );
  } else if (LookedFamiliardifference == 0) {
    familiarwords = (
      <Typography color="secondary">
        average amount of looked familiar dreams
      </Typography>
    );
  } else if (LookedFamiliardifference < 0) {
    familiarwords = (
      <Typography color="secondary">
        {Math.abs(LookedFamiliardifference)}% less looked familiar dreams
      </Typography>
    );
  }
  let pastwords = "";
  if (peoplefrommypastdifference > 0) {
    pastwords = (
      <Typography color="secondary">
        {Math.abs(peoplefrommypastdifference)}% more past people dreams
      </Typography>
    );
  } else if (peoplefrommypastdifference == 0) {
    pastwords = (
      <Typography color="secondary">
        average amount of past people dreams
      </Typography>
    );
  } else if (peoplefrommypastdifference < 0) {
    pastwords = (
      <Typography color="secondary">
        {Math.abs(peoplefrommypastdifference)}% less past people dreams
      </Typography>
    );
  }
  let familywords = "";
  if (Familydifference > 0) {
    familywords = (
      <Typography color="secondary">
        {Math.abs(Familydifference)}% more family dreams
      </Typography>
    );
  } else if (Familydifference == 0) {
    familywords = (
      <Typography color="secondary">average amount of family dreams</Typography>
    );
  } else if (Familydifference < 0) {
    familywords = (
      <Typography color="secondary">
        {Math.abs(Familydifference)}% less family dreams
      </Typography>
    );
  }
  let animalwords = "";
  if (animalsdifference > 0) {
    animalwords = (
      <Typography color="secondary">
        {Math.abs(animalsdifference)}% more animal dreams
      </Typography>
    );
  } else if (animalsdifference == 0) {
    animalwords = (
      <Typography color="secondary">average amount of animal dreams</Typography>
    );
  } else if (animalsdifference < 0) {
    animalwords = (
      <Typography color="secondary">
        {Math.abs(animalsdifference)}% less animal dreams
      </Typography>
    );
  }
  const Characterdata = {
    labels: [
      "Friends",
      "Monsters",
      "No People",
      "Family",
      "Celebrities",
      "People from my Past",
      "Strangers",
      "Looked Familiar",
      "Animals",
    ],
    datasets: [
      {
        label: "",
        backgroundColor: [
          "white",
          "red",
          "purple",
          "orange",
          "green",
          "yellow",
          "blue",
          "pink",
          "teal",
        ],
        borderColor: "rgba(255,99,132,1)",
        borderWidth: 0,
        hoverBackgroundColor: "rgba(255,99,132,0.4)",
        hoverBorderColor: "rgba(255,99,132,1)",
        data: characterdiffarray,
      },
    ],
  };

  switch (scene) {
    case "words": {
      return (
        <div>
          {" "}
          <Typography variant="h5" color="secondary">
            Compared to other's you have:
          </Typography>
          <Grid container justify="center">
            <Grid item>
              <Box borderRadius="50%">
                <Paper elevation="5">
                  <li className="charbox">
                    <Divider />
                    {friendswords}
                    <Divider />
                    {monsterwords}
                    <Divider />
                    {nopeoplewords}
                    <Divider />
                    {friendswords}
                    <Divider />
                    {celebritywords}
                    <Divider />
                    {Strangerwords}
                    <Divider />
                    <Divider />
                    {familiarwords}
                    <Divider />
                    {pastwords}
                    <Divider />
                    {familywords}
                    <Divider />
                    {animalwords}
                    <Divider />
                  </li>
                </Paper>
              </Box>
            </Grid>
          </Grid>
          <select
            value={scene}
            onChange={(e) => setscene(e.currentTarget.value)}
          >
            <option value="words">words</option>
            <option value="bar">Bar</option>
          </select>
        </div>
      );
    }
    case "bar": {
      return (
        <div>
          <Typography variant="h5" color="secondary">
            Compared to Average:
          </Typography>
          <Bar data={Characterdata} />
          <select
            value={scene}
            onChange={(e) => setscene(e.currentTarget.value)}
          >
            <option value="words">words</option>
            <option value="bar">Bar</option>
          </select>
        </div>
      );
    }
  }
  return <div></div>;
}

export default Usercomparison;
