import React, { useState, useEffect, useContext } from "react";
import firebase from "../firebase";
import { AuthContext } from "../../Auth";

import { Pie, Line, Bar, Radar, Polar, Area } from "react-chartjs-2";
import { Button, Typography, Grid, Card } from "@material-ui/core";
function TimeData() {
  const [dreams, setdreams] = useState([]);
  const [page, setpage] = useState("Pie");
  const { currentUser } = useContext(AuthContext);
  const changepage = (page) => {
    setpage(page);
  };
  useEffect(() => {
    const fetchData = async () => {
      const db = firebase.firestore();
      const data = await db
        .collection("Users")
        .doc(currentUser.email)
        .collection("dreams")
        .get();
      setdreams(data.docs.map((doc) => doc.data()));
    };
    fetchData();
  }, []);
  const timelistlist = dreams.map((dream) => {
    return dream.Time;
  });

  const countOccurrences = function (arr, val) {
    return arr.reduce((acc, elem) => {
      return val === elem ? acc + 1 : acc;
    }, 0);
  };

  const mergetime = [].concat.apply([], timelistlist);

  const TimeNormaloccurrance = countOccurrences(mergetime, "Normal");
  const TimeVeryLongoccurrance = countOccurrences(mergetime, "Very Long");
  const TimeShortoccurrance = countOccurrences(mergetime, "Short");
  const TimeLongoccurrance = countOccurrences(mergetime, "Long");
  const TimeVeryShortoccurrance = countOccurrences(mergetime, "Very Short");
  const Timelist = [
    TimeVeryLongoccurrance,
    TimeLongoccurrance,
    TimeNormaloccurrance,
    TimeShortoccurrance,
    TimeVeryShortoccurrance,
  ];

  const arrSum = (arr) => arr.reduce((a, b) => a + b, 0);
  const long = arrSum(Timelist);
  const Timedata = {
    labels:  ["Very Long", "Long", "Normal", "Short", " Very Short"],
    datasets: [
      {
        backgroundColor: [
          "red",
          "blue",
          "green",
          "white",
          "orange",
          "purple",
          "yellow",
          "black",
        ],
        borderColor: "rgba(255,99,132,1)",
        borderWidth: 0,
        hoverBackgroundColor: "rgba(255,99,132,0.4)",
        hoverBorderColor: "rgba(255,99,132,1)",
        data: Timelist,
      },
    ],
  };

  switch (page) {
    case "Line":
      return (
        <div>
          <Typography color="primary" variant="h4">
            Your Time
          </Typography>
          <Line data={Timedata} />
          <select value={page} onChange={(e) => setpage(e.currentTarget.value)}>
            <option value="Line">Line</option>
            <option value="Bar">Bar</option>
            <option value="Radar">Radar</option>
            <option value="Area">Area</option>
            <option value="Pie">Pie</option>
          </select>
        </div>
      );

    case "Pie":
      if (long == 0) {
        return null;
      } else
        return (
          <div>
            <Typography color="primary" variant="h4">
              Your Time
            </Typography>
            <Pie data={Timedata} />
            <select
              value={page}
              onChange={(e) => setpage(e.currentTarget.value)}
            >
              <option value="Line">Line</option>
              <option value="Bar">Bar</option>
              <option value="Radar">Radar</option>
              <option value="Area">Area</option>
              <option value="Pie">Pie</option>
            </select>
          </div>
        );
    case "Bar":
      return (
        <div>
          <Typography color="primary" variant="h4">
            Your Time
          </Typography>

          <Bar data={Timedata} />
          <select value={page} onChange={(e) => setpage(e.currentTarget.value)}>
            <option value="Line">Line</option>
            <option value="Bar">Bar</option>
            <option value="Radar">Radar</option>
            <option value="Area">Area</option>
            <option value="Pie">Pie</option>
          </select>
        </div>
      );
    case "Radar":
      return (
        <div>
          <Typography color="primary" variant="h4">
            Your Time
          </Typography>
          <Radar data={Timedata} />
          <select value={page} onChange={(e) => setpage(e.currentTarget.value)}>
            <option value="Line">Line</option>
            <option value="Bar">Bar</option>
            <option value="Radar">Radar</option>
            <option value="Area">Area</option>
            <option value="Pie">Pie</option>
          </select>
        </div>
      );
    case "Area":
      return (
        <div>
          <Typography color="primary" variant="h4">
            Your Time
          </Typography>
          <Polar data={Timedata} />
          <select value={page} onChange={(e) => setpage(e.currentTarget.value)}>
            <option value="Line">Line</option>
            <option value="Bar">Bar</option>
            <option value="Radar">Radar</option>
            <option value="Area">Area</option>
            <option value="Pie">Pie</option>
          </select>
        </div>
      );
    default:
      break;
  }
}

export default TimeData;
