import React, { useState, useEffect } from "react";
import firebase from "./firebase";
import { Pie, Line, Bar, Radar, Polar, Area } from "react-chartjs-2";
import { Button, Typography, Grid, Card } from "@material-ui/core";
function CharacterData() {
  const [dreams, setdreams] = useState([]);
  const [page, setpage] = useState("Pie");
  const changepage = (page) => {
    setpage(page);
  };

  useEffect(() => {
    const fetchData = async () => {
      const db = firebase.firestore();
      const data = await db.collection("Dream Total").get();
      setdreams(data.docs.map((doc) => doc.data()));
    };
    fetchData();
  }, []);

  const characterlistlist = dreams.map((dream) => {
    return dream.Character;
  });

  const countOccurrences = function (arr, val) {
    return arr.reduce((acc, elem) => {
      return val === elem ? acc + 1 : acc;
    }, 0);
  };
  const mergecharacter = [].concat.apply([], characterlistlist);
  const charnopeopleoccurrence = countOccurrences(mergecharacter, "No People");
  const charcelebritiesoccurrence = countOccurrences(
    mergecharacter,
    "Celebrities"
  );
  const charfamilyoccurrence = countOccurrences(mergecharacter, "Family");
  const charfriendsoccurrence = countOccurrences(mergecharacter, "Friends");
  const charpastpeopleoccurrence = countOccurrences(
    mergecharacter,
    "People from my Past"
  );
  const charcelebrities = countOccurrences(mergecharacter, "Celebrities");
  const charlookedfamiliar = countOccurrences(
    mergecharacter,
    "Looked Familiar"
  );
  const charstrangers = countOccurrences(mergecharacter, "Strangers");
  const charmonsters = countOccurrences(mergecharacter, "Monsters");
  const charanimals = countOccurrences(mergecharacter, "Animals");
  const characterlist = [
    charfriendsoccurrence,
    charmonsters,
    charnopeopleoccurrence,
    charfamilyoccurrence,
    charcelebritiesoccurrence,
    charpastpeopleoccurrence,
    charstrangers,
    charlookedfamiliar,
    charanimals,
  ];

  const chardata = {
    labels: [
      "Friends",
      "Monsters",
      "No People",
      "Family",
      "Celebrities",
      "People from my Past",
      "Strangers",
      "Looked Familiar",
      "Animals",
    ],
    datasets: [
      {
        backgroundColor: [
          "white",
          "red",
          "purple",
          "orange",
          "green",
          "yellow",
          "blue",
          "pink",
          "teal",
        ],
        borderColor: "rgba(255,99,132,1)",
        borderWidth: 0,
        hoverBackgroundColor: "rgba(255,99,132,0.4)",
        hoverBorderColor: "rgba(255,99,132,1)",
        data: characterlist,
      },
    ],
  };

  switch (page) {
    case "Line":
      return (
        <div>
          <Typography color="primary" variant="h5">
            Global Characters
          </Typography>

          <Line data={chardata} />
          <select value={page} onChange={(e) => setpage(e.currentTarget.value)}>
            <option value="Line">Line</option>
            <option value="Bar">Bar</option>
            <option value="Radar">Radar</option>
            <option value="Area">Area</option>
            <option value="Pie">Pie</option>
          </select>
        </div>
      );

    case "Pie":
      return (
        <div>
          <Typography color="primary" variant="h5">
            Global Characters
          </Typography>
          <Pie data={chardata} />
          <select value={page} onChange={(e) => setpage(e.currentTarget.value)}>
            <option value="Line">Line</option>
            <option value="Bar">Bar</option>
            <option value="Radar">Radar</option>
            <option value="Area">Area</option>
            <option value="Pie">Pie</option>
          </select>
        </div>
      );
    case "Bar":
      return (
        <div>
          <Typography color="primary" variant="h5">
            Global Characters
          </Typography>

          <Bar data={chardata} />
          <select value={page} onChange={(e) => setpage(e.currentTarget.value)}>
            <option value="Line">Line</option>
            <option value="Bar">Bar</option>
            <option value="Radar">Radar</option>
            <option value="Area">Area</option>
            <option value="Pie">Pie</option>
          </select>
        </div>
      );
    case "Radar":
      return (
        <div>
          <Typography color="primary" variant="h5">
            Global Characters Area Chart
          </Typography>
          <Radar data={chardata} />
          <select value={page} onChange={(e) => setpage(e.currentTarget.value)}>
            <option value="Line">Line</option>
            <option value="Bar">Bar</option>
            <option value="Radar">Radar</option>
            <option value="Area">Area</option>
            <option value="Pie">Pie</option>
          </select>
        </div>
      );
    case "Area":
      return (
        <div>
          <Typography color="primary" spacing="3" variant="h5">
            Global Characters
          </Typography>
          <Polar data={chardata} />
          <select value={page} onChange={(e) => setpage(e.currentTarget.value)}>
            <option value="Line">Line</option>
            <option value="Bar">Bar</option>
            <option value="Radar">Radar</option>
            <option value="Area">Area</option>
            <option value="Pie">Pie</option>
          </select>
        </div>
      );

    default:
      break;
  }
}

export default CharacterData;
