import React from "react";
import "./App.css";
import Home from "./Components/Home";
import { BrowserRouter as Router, Route } from "react-router-dom";

import Login from "./Components/Login";
import SignUp from "./Components/SignUp";
import { AuthProvider } from "./Auth";
import PrivateRoute from "./PrivateRoute";
import LearnMore from "./Components/LearnMore";
import Sky from "react-sky";
import cloud from "./Components/images/cloud.png";

const App = () => {
  return (
    <AuthProvider>
      <Router>
        <div className="App">
          <Sky
            images={{
              /* FORMAT AS FOLLOWS */
              0: cloud,
            }}
            how={
              100
            } /* You have to pass a number so Sky will render that amount of images chosen randomly from the object you passed in the previous step */
            time={60} /* time of the animation. Dfaults at 20s */
            size={"70px"} /* size of the rendered images. Defaults at 150px */
            background={"black"} /* color of background. Defaults to none */
          />
          {console.log("test")}
          <PrivateRoute exact path="/DreamBuddy" component={Home} />
          <Route exact path="/login" component={Login} />
          <Route exact path="/signup" component={SignUp} />
          <Route exact path="/LearnMore" component={LearnMore} />
        </div>
      </Router>
    </AuthProvider>
  );
};

export default App;
