import React, { useState } from "react";
import {
  Button,
  Typography,
  Grid,
  TextField,
  Paper,
  Slider,
} from "@material-ui/core";
import "./Intensity.css";
import Sky from "react-sky";
import cloud from "../Components/images/cloud.png";
function Intensity(props) {
  return (
    <div className="Intensity">
      <br />
      <Grid container spacing={4} justify="center">
        <div className="mainblackout">
          <Grid item xs={12}>
            <Typography color="secondary" variant="h4">
              Emotion Intensity
            </Typography>
          </Grid>
        </div>
        <Grid item xs={12}>
          <Grid container justify="center">
            <div className="mainblackout">
              <Grid item>
                <Typography color="primary" variant="h2">
                  {props.ActiRange}
                  {props.ActiRange === "" ? null : "%"}
                </Typography>
              </Grid>
            </div>
          </Grid>
        </Grid>
        <Grid container>
          <Grid item xs={4}>
            <div className="verymild">
              <Typography color="secondary" variant="h5">
                Very
                <br /> Mild
              </Typography>
            </div>
          </Grid>
          <Grid item xs={4}>
            <div className="verymild">
              <Slider
                aria-labelledby="discrete-slider-small-steps"
                arial-label="so does it work"
                defaultValue={50}
                valueLabelDisplay="auto"
                step={1}
                onChange={(e, value) => props.setActiRange(value)}
              />
            </div>
          </Grid>
          <Grid item xs={4}>
            <div className="verymild">
              <Typography color="secondary" variant="h5">
                Very <br /> Intense
              </Typography>
            </div>
          </Grid>
        </Grid>

        <Grid container spacing={3} justify="center">
          <Grid item>
            <Button color="primary" variant="contained" onClick={props.prev}>
              Back
            </Button>
          </Grid>
          <Grid item>
            <Button color="primary" variant="contained" onClick={props.next}>
              Next
            </Button>
          </Grid>
        </Grid>
      </Grid>
    </div>
  );
}

export default Intensity;
