import React, { useState } from "react";
import { Button, Grid, Drawer, ListItem, List } from "@material-ui/core";
import ClickAwayListener from "@material-ui/core/ClickAwayListener";
import "./Nav.css";
import { auth } from "./firebase";
import YourDreams from "./YourDreams/YourDreams";
function Nav(props) {
  const YourDreams = () => {
    props.closedrawer();
    props.yourdreamz();
  };
  const DreamerWeb = () => {
    props.closedrawer();
    props.dreamweb();
  };
  const GlobalDreamData = () => {
    props.closedrawer();
    props.globaldreamerdata();
  };
  const yourdreamdata = () => {
    props.closedrawer();
    props.yourdreamweb();
  };
  const startdream = () => {
    props.closedrawer();
    props.logdream();
  };

  const signout = () => {
    return auth.signOut();
  };

  return (
    <div>
      <div className="fixedNav">
        <Button
          onClick={() => {
            props.handleDrawer();
          }}
          variant="contained"
          color="secondary"
        >
          Menu
        </Button>
      </div>

      <Drawer anchor="left" open={props.open}>
        <Button onClick={props.closedrawer} size="large">
          X
        </Button>
        <ListItem>
          <Button
            variant="contained"
            color="primary"
            onClick={() => {
              signout();
            }}
          >
            sign out
          </Button>
        </ListItem>
        <ListItem>
          <Button
            variant="contained"
            color="secondary"
            onClick={YourDreams}
            size="large"
          >
            Your Dreams
          </Button>
        </ListItem>
        <ListItem>
          <Button
            variant="contained"
            color="primary"
            onClick={DreamerWeb}
            size="large"
          >
            Dream Web
          </Button>
        </ListItem>
        <ListItem>
          <Button
            onClick={GlobalDreamData}
            color="secondary"
            variant="contained"
            size="large"
          >
            Global Dream Data
          </Button>
        </ListItem>
        <ListItem>
          <Button
            onClick={startdream}
            color="primary"
            variant="contained"
            size="large"
          >
            Report Dream
          </Button>
        </ListItem>
        <ListItem>
          <Button
            onClick={yourdreamdata}
            color="secondary"
            variant="contained"
            size="large"
          >
            Your Dream Data
          </Button>
        </ListItem>
      </Drawer>
    </div>
  );
}

export default Nav;
