import React, { useEffect, useState, useContext } from "react";
import firebase from "../firebase";
import { AuthContext } from "../../Auth";
import { Button, Typography, Grid, Card } from "@material-ui/core";
function YourEmoIntensity() {
  const { currentUser } = useContext(AuthContext);
  const [dreams, setdreams] = useState([]);

  useEffect(() => {
    const fetchData = async () => {
      const db = firebase.firestore();
      const data = await db
        .collection("Users")
        .doc(currentUser.email)
        .collection("dreams")
        .get();
      setdreams(data.docs.map((doc) => doc.data()));
    };
    fetchData();
  }, []);
  const actilistlist = dreams.map((dream) => {
    if (dream.ActiRange) {
      return dream.ActiRange;
    }
  });

  const arrSum = (arr) => arr.reduce((a, b) => a + b, 0);
  const long = arrSum(actilistlist);

  const actionlist = actilistlist.filter((item) => item !== undefined);
  const arrAvg = (arr) => arr.reduce((a, b) => a + b, 0) / arr.length;
  if (long == 0) {
    return null;
  }
  return (
    <div>
      <Typography color="primary" variant="h5">
        Average Emotion Intensity
      </Typography>

      <Typography color="secondary" variant="h5">
        {Math.round(arrAvg(actionlist))}%
      </Typography>
    </div>
  );
}

export default YourEmoIntensity;
