import React, { useState, useContext, useEffect } from "react";
import Begin from "./Begin/Begin";
import Emo from "./Emo";
import Intensity from "./Intensity";
import Character from "./Character";
import Times from "./Times";
import Reality from "./Reality";
import Location from "./Location";
import Report from "./Report";
import Description from "./Description";
import "./Control.css";
import Discover from "./Discover";
import firebase from "./firebase";
import Name from "./Name";
import { AuthContext } from "../Auth";
import Useremotions from "./Comparion/Useremotion";
import Charactercomparison from "./Comparion/Charactercomparison";
import Sure from "./Sure";
import { v4 as uuidv4 } from "uuid";

import {
  ListItem,
  Typography,
  Button,
  Drawer,
  Menu,
  IconButton,
  Grid,
} from "@material-ui/core";
import YourDreams from "./YourDreams/YourDreams";
import Nav from "./Nav";
import YourDreamWrapper from "./YourDreamData/YourDreamWrapper";

import GlobalData from "./GlobalData";
import DreamList from "./dream-list/dream-list";

function Control() {
  const [page, setpage] = useState(1);
  const [dreams, setdreams] = useState();
  const [Emolist, setEmolist] = useState([]);
  const [emomore, setemomore] = useState({
    objects: [
      { id: "Fear", toggled: false },
      { id: "Anger", toggled: false },
      { id: "Sadness", toggled: false },
      { id: "Joy", toggled: false },
      { id: "Disgust", toggled: false },
      { id: "Surprise", toggled: false },
      { id: "Trust", toggled: false },
      { id: "Lust", toggled: false },
    ],
  });
  const [Time, setTime] = useState("");
  const [moreTime, setmoreTime] = useState({
    objects: [
      { id: "Very Short" },
      { id: "Short" },
      { id: "Normal" },
      { id: "Long" },
      { id: "Very Long" },
    ],
  });
  const [name, setname] = useState("");
  const [ActiRange, setActiRange] = useState("");
  const [character, setcharacter] = useState([]);
  const [charactermore, setcharactermore] = useState({
    objects: [
      { id: "No People", toggled: false },
      { id: "Family", toggled: false },
      { id: "Friends", toggled: false },
      { id: "Looked Familiar", toggled: false },
      { id: "People from my Past", toggled: false },
      { id: "Strangers", toggled: false },
      { id: "Monsters", toggled: false },
      { id: "Celebrities", toggled: false },
      { id: "Animals", toggled: false },
    ],
  });
  const [location, setlocation] = useState([]);
  const [RealityValue, setRealityValue] = useState("");
  const [description, setdescription] = useState("");
  const [dreamlogged, setdreamlogged] = useState(false);
  const [open, setopen] = useState(false);

  const handleDrawer = () => {
    setopen(true);
  };
  const closedrawer = () => {
    setopen(false);
  };

  const { currentUser } = useContext(AuthContext);

  const logname = (name) => {
    setname(name);
  };

  const LogTime = (tim) => {
    if (Time !== tim) {
      setTime(tim);
    } else {
      setTime("");
    }
  };

  const nextpage = () => {
    setpage(page + 1);
  };
  const deleteemo = (index) => {
    let newEmolist = Emolist;

    newEmolist.splice(index, 1);
    setEmolist([...newEmolist]);
  };
  const logEmo = (emo) => {
    if (Emolist.includes(emo)) {
      return deleteemo();
    } else setEmolist([...Emolist, emo]);
  };
  function emotoggleActive(element, index) {
    let arrayCopy = [...emomore.objects];
    let emoCopy = [...Emolist];
    arrayCopy[index].toggled
      ? (arrayCopy[index].toggled = false)
      : (arrayCopy[index].toggled = true);
    if (arrayCopy[index].toggled) {
      setEmolist([emomore.objects[index].id, ...emoCopy]);
    } else {
      let newarray = emoCopy.filter((deleted) => !deleted.includes(element.id));
      setEmolist([...newarray]);
    }
  }

  function chartoggleActive(element, index) {
    let arrayCopy = [...charactermore.objects];
    let charCopy = [...character];
    arrayCopy[index].toggled
      ? (arrayCopy[index].toggled = false)
      : (arrayCopy[index].toggled = true);
    if (arrayCopy[index].toggled) {
      setcharacter([charactermore.objects[index].id, ...charCopy]);
    } else {
      let newarray = charCopy.filter(
        (deleted) => !deleted.includes(element.id)
      );
      setcharacter([...newarray]);
    }
  }

  function changeemocolor(index) {
    if (emomore.objects[index].toggled) {
      return "primary";
    } else {
      return "secondary";
    }
  }

  function changecharcolor(index) {
    if (charactermore.objects[index].toggled) {
      return "primary";
    } else {
      return "secondary";
    }
  }
  function changetimecolor(index) {
    if (Time === index) {
      return "primary";
    } else {
      return "secondary";
    }
  }
  const deletechar = (index) => {
    let newchar = character;
    newchar.splice(index, 1);
    setcharacter([...newchar]);
  };
  // function deletechar(id) {
  //   const newList = character.filter((item) => item.id !== id);
  //   setcharacter(newList);
  // }

  const logCharacter = (ch) => {
    let newchar = character;
    character.includes(ch) ? deletechar(ch) : setcharacter([ch, ...newchar]);
    // let newchar = character;
    // if (character.includes(ch)) {
    //   return deletechar(ch);
    // } else setcharacter([ch, ...newchar]);
  };

  // firebase database functions

  const LogDataBase = () => {
    firebase.firestore().collection("Public Dream").add({
      Name: name,
      Emolist: Emolist,
      Time: Time,
      ActiRange: ActiRange,
      Character: character,
      location: location,
      RealityValue: RealityValue,
      Description: description,
      id: uuidv4(),
      timestamp: new Date(),
    });
    setdreamlogged(!dreamlogged);
  };

  // const mergeemototal = [].concat.apply([], dreams);
  useEffect(() => {
    const fetchData = async () => {
      const db = firebase.firestore();
      const data = await db
        .collection("Users")
        .doc(currentUser.email)
        .collection("dreams")
        .get();
      setdreams(data.docs.map((doc) => doc.data()));
    };
    fetchData();
  }, []);

  //user based database
  console.log(currentUser.uid);
  const logpersonaldream = () => {
    firebase
      .firestore()
      .collection("Users")
      .doc(currentUser.email)
      .collection("dreams")
      .add({
        Name: name,
        Emolist: Emolist,
        Time: Time,
        ActiRange: ActiRange,
        Character: character,
        location: location,
        RealityValue: RealityValue,
        id: uuidv4(),
        Description: description,
        timestamp: new Date(),
        uid: currentUser.uid,
      });

    // Private only for calculations
    firebase.firestore().collection("Dream Total").add({
      Name: name,
      Emolist: Emolist,
      Time: Time,
      ActiRange: ActiRange,
      Character: character,
      location: location,
      RealityValue: RealityValue,
      id: uuidv4(),
      uid: currentUser.uid,
      Description: description,
      timestamp: new Date(),
    });

    nextpage();
  };
  const personaldream = () => {
    setpage(12);
  };
  const dreamweb = () => {
    setpage(11);
  };
  const yourdreams = () => {
    setpage(page + 2);
  };
  const homepage = () => {
    setpage(1);
  };

  function prevPage() {
    setpage(page + 1);
  }
  function globaldreamdata() {
    setpage(13);
  }
  function startdreampage() {
    setpage(2);
  }
  function yourdreamweb() {
    setpage(14);
  }
  console.log(page);

  switch (page) {
    case 1:
      return (
        <div className="Reportpage">
          <Nav
            homepage={homepage}
            dreamweb={dreamweb}
            personaldream={personaldream}
            globaldreamerdata={globaldreamdata}
            handleDrawer={handleDrawer}
            open={open}
            yourdreamweb={yourdreamweb}
            closedrawer={closedrawer}
            logdream={startdreampage}
            yourdreamz={personaldream}
          />

          <Begin start={nextpage} />

          <YourDreamWrapper />
        </div>
      );

    case 2:
      return (
        <div>
          <Nav
            homepage={homepage}
            dreamweb={dreamweb}
            personaldream={personaldream}
            globaldreamerdata={globaldreamdata}
            handleDrawer={handleDrawer}
            open={open}
            yourdreamweb={yourdreamweb}
            closedrawer={closedrawer}
            logdream={startdreampage}
            yourdreamz={personaldream}
          />
          <Description
            setdescription={setdescription}
            description={description}
            next={nextpage}
            prev={prevPage}
          />
        </div>
      );

    case 3:
      return (
        <div>
          <Nav
            homepage={homepage}
            dreamweb={dreamweb}
            personaldream={personaldream}
            globaldreamerdata={globaldreamdata}
            handleDrawer={handleDrawer}
            open={open}
            yourdreamweb={yourdreamweb}
            closedrawer={closedrawer}
            logdream={startdreampage}
            yourdreamz={personaldream}
          />

          <Emo
            // logemo={logEmo}
            logEmo={logEmo}
            nextpage={nextpage}
            prev={prevPage}
            Emolist={Emolist}
            deleteemo={deleteemo}
            setemomore={setemomore}
            emomore={emomore}
            togglecolor={changeemocolor}
            emotoggleActive={emotoggleActive}
          />

          {console.log(Emolist)}
        </div>
      );
    case 4:
      return (
        <div>
          <Nav
            homepage={homepage}
            dreamweb={dreamweb}
            personaldream={personaldream}
            globaldreamerdata={globaldreamdata}
            handleDrawer={handleDrawer}
            open={open}
            yourdreamweb={yourdreamweb}
            closedrawer={closedrawer}
            logdream={startdreampage}
            yourdreamz={personaldream}
          />
          <Intensity
            ActiRange={ActiRange}
            next={nextpage}
            setActiRange={setActiRange}
            prev={prevPage}
          />
        </div>
      );

    case 5:
      return (
        <div>
          <Nav
            homepage={homepage}
            dreamweb={dreamweb}
            personaldream={personaldream}
            globaldreamerdata={globaldreamdata}
            handleDrawer={handleDrawer}
            open={open}
            yourdreamweb={yourdreamweb}
            closedrawer={closedrawer}
            logdream={startdreampage}
            yourdreamz={personaldream}
          />
          {console.log(character)}
          <Character
            character={character}
            deletechar={deletechar}
            logCharacter={logCharacter}
            next={nextpage}
            prev={prevPage}
            charactermore={charactermore}
            changecharcolor={changecharcolor}
            chartoggleActive={chartoggleActive}
          />
        </div>
      );

    case 6:
      return (
        <div>
          <Nav
            homepage={homepage}
            dreamweb={dreamweb}
            personaldream={personaldream}
            globaldreamerdata={globaldreamdata}
            handleDrawer={handleDrawer}
            open={open}
            yourdreamweb={yourdreamweb}
            closedrawer={closedrawer}
            logdream={startdreampage}
            yourdreamz={personaldream}
          />
          <Times
            Time={Time}
            LogTime={LogTime}
            prev={prevPage}
            next={nextpage}
            moreTime={moreTime}
            changetimecolor={changetimecolor}
          />
        </div>
      );
    case 7:
      return (
        <div>
          <Nav
            homepage={homepage}
            dreamweb={dreamweb}
            personaldream={personaldream}
            globaldreamerdata={globaldreamdata}
            handleDrawer={handleDrawer}
            open={open}
            yourdreamweb={yourdreamweb}
            closedrawer={closedrawer}
            logdream={startdreampage}
            yourdreamz={personaldream}
          />
          <Location setlocation={setlocation} next={nextpage} prev={prevPage} />
        </div>
      );

    case 8:
      return (
        <div>
          <Nav
            homepage={homepage}
            dreamweb={dreamweb}
            personaldream={personaldream}
            globaldreamerdata={globaldreamdata}
            handleDrawer={handleDrawer}
            open={open}
            yourdreamweb={yourdreamweb}
            closedrawer={closedrawer}
            logdream={startdreampage}
            yourdreamz={personaldream}
          />
          <Reality
            setRealityValue={setRealityValue}
            RealityValue={RealityValue}
            next={nextpage}
            prev={prevPage}
          />
        </div>
      );

    case 9:
      return (
        <div>
          <Nav
            homepage={homepage}
            dreamweb={dreamweb}
            personaldream={personaldream}
            globaldreamerdata={globaldreamdata}
            handleDrawer={handleDrawer}
            open={open}
            yourdreamweb={yourdreamweb}
            closedrawer={closedrawer}
            logdream={startdreampage}
            yourdreamz={personaldream}
          />
          <Name
            submit={logpersonaldream}
            name={name}
            setname={setname}
            prev={prevPage}
            logdream={() => {
              logname();
            }}
          />
        </div>
      );
    case 10:
      return (
        <div>
          <Nav
            homepage={homepage}
            dreamweb={dreamweb}
            personaldream={personaldream}
            globaldreamerdata={globaldreamdata}
            handleDrawer={handleDrawer}
            open={open}
            yourdreamweb={yourdreamweb}
            closedrawer={closedrawer}
            logdream={startdreampage}
            yourdreamz={personaldream}
          />
          <Grid justify="center" container>
            <Grid item>
              {/* <Sure dreamlogged={LogDataBase} /> */}
              <Report
                prev={prevPage}
                name={name}
                Time={Time}
                Emolist={Emolist}
                ActiRange={ActiRange}
                character={character}
                Reality={RealityValue}
                Description={description}
                next={nextpage}
                location={location}
                logdream={LogDataBase}
                dreamlogged={dreamlogged}
                yourdreams={yourdreams}
                yourdata={yourdreamweb}
              />
            </Grid>
          </Grid>
        </div>
      );
    case 11:
      return (
        <div className="yourdream">
          <Nav
            homepage={homepage}
            dreamweb={dreamweb}
            personaldream={personaldream}
            globaldreamerdata={globaldreamdata}
            handleDrawer={handleDrawer}
            open={open}
            yourdreamweb={yourdreamweb}
            closedrawer={closedrawer}
            logdream={startdreampage}
            yourdreamz={personaldream}
          />

          <DreamList />
        </div>
      );
    case 12:
      return (
        <div>
          <Nav
            homepage={homepage}
            dreamweb={dreamweb}
            personaldream={personaldream}
            globaldreamerdata={globaldreamdata}
            handleDrawer={handleDrawer}
            open={open}
            yourdreamweb={yourdreamweb}
            closedrawer={closedrawer}
            logdream={startdreampage}
            yourdreamz={personaldream}
          />
          <Grid container justify="center">
            <Grid item>
              <YourDreams startdream={startdreampage} prevPage={prevPage} />
            </Grid>
          </Grid>
        </div>
      );
    case 13:
      return (
        <div className="globaldata">
          <Nav
            homepage={homepage}
            dreamweb={dreamweb}
            personaldream={personaldream}
            globaldreamerdata={globaldreamdata}
            handleDrawer={handleDrawer}
            open={open}
            yourdreamweb={yourdreamweb}
            closedrawer={closedrawer}
            logdream={startdreampage}
            yourdreamz={personaldream}
          />

          <GlobalData />
        </div>
      );
    case 14:
      return (
        <div>
          <Nav
            homepage={homepage}
            dreamweb={dreamweb}
            personaldream={personaldream}
            globaldreamerdata={globaldreamdata}
            handleDrawer={handleDrawer}
            open={open}
            yourdreamweb={yourdreamweb}
            closedrawer={closedrawer}
            logdream={startdreampage}
            yourdreamz={personaldream}
          />

          <YourDreamWrapper />
        </div>
      );
  }
}

export default Control;

//name component
