import React from "react";
import { Typography, Grid } from "@material-ui/core";
import "./Banner.css";
function Banner() {
  return (
  
      <Grid container justify="center">
        <div className="dreamblack">
          <Grid item>
            <Typography color="primary" gutterBottom variant="h4">
              Dreamer Buddy
            </Typography>
          </Grid>
        </div>
      </Grid>
         
  
  );
}

export default Banner;
