import React, { useState, useEffect } from "react";
import firebase from "../firebase";
import "./dream-list.css";
import {
  Button,
  Typography,
  Grid,
  Paper,
  Select,
  MenuItem,
} from "@material-ui/core";
import down from "../images/down.png";

const SORT_OPTIONS = {
  TimestampAsc: { column: "timestamp", direction: "asc" },
  TimestampDesc: { column: "timestamp", direction: "desc" },
  NameAsc: { column: "Name", direction: "asc" },
  NameDesc: { column: "Name", direction: "desc" },
  LocationAsc: { column: "location", direction: "asc" },
  LocationDesc: { column: "location", direction: "desc" },
  RealityAsc: { column: "RealityValue", direction: "desc" },
  RealityDesc: { column: "RealityValue", direction: "asc" },
  ActiRangeAsc: { column: "ActiRange", direction: "asc" },
  ActiRangeDesc: { column: "ActiRange", direction: "desc" },
};

function useDream(sortBy = "TIME_ASC") {
  const [dreams, setdreams] = useState([]);

  useEffect(() => {
    const unsubscribe = firebase
      .firestore()
      .collection("Public Dream")
      .orderBy(SORT_OPTIONS[sortBy].column, SORT_OPTIONS[sortBy].direction)
      .onSnapshot((snapshot) => {
        const newDreams = snapshot.docs.map((doc) => ({
          id: doc.id,
          ...doc.data(),
        }));
        setdreams(newDreams);
      });
    return () => unsubscribe();
  }, [sortBy]);

  return dreams;
}
const DreamList = (props) => {
  const [sortBy, setSortBy] = useState("TimestampAsc");
  const [swap, setswap] = useState(false);
  const toggleswap = (index) => {
    setswap(!swap);
  };
  const dreams = useDream(sortBy);

  return (
    <div className="containera">
      <Typography color="secondary" variant="h4">
        Dream Web
      </Typography>

      <label>Sort By:</label>
      <select value={sortBy} onChange={(e) => setSortBy(e.currentTarget.value)}>
        <option value="TimestampAsc">Time (first)</option>
        <option value="TimestampDesc">Time (recent)</option>
        <option>--</option>
        <option value="NameAsc"> Name A</option>
        <option value="NameDesc">Name Z</option>
        <option>--</option>
        <option value="LocationAsc">Location A</option>
        <option value="LocationDesc">Location Z</option>
        <option>--</option>
        <option value="RealityAsc">Most Real</option>
        <option value="RealityDesc">Least Real</option>
        <option>--</option>
        <option value="ActiRangeAsc">Most Emotionally Intense</option>
        <option value="ActiRangeDesc">Least Emotionally Intense</option>
      </select>

      <ul className="containera">
        {dreams.map((dream, index) => {
          return (
            <li key={index} className="dreamli">
              <Grid container justify="center">
                <Grid item>
                  <div className="dreamcardwrap">
                    {console.log(swap)}
                    <Typography variant="h4" color="primary">
                      {dream.Name}
                    </Typography>
                    {swap && dream.Description.length > 0 ? (
                      <div>
                        <Typography color="secondary" variant="h5">
                          Description
                        </Typography>
                        <Typography color="primary" variant="h6">
                          {dream.Description}
                        </Typography>
                      </div>
                    ) : null}
                    {swap && dream.Emolist.length > 0 ? (
                      <div>
                        <Typography color="secondary" variant="h5">
                          Emotions
                        </Typography>
                        <Typography color="primary" variant="h6">
                          {dream.Emolist.map((e) => {
                            return <li>{e}</li>;
                          })}
                        </Typography>
                      </div>
                    ) : null}
                    {swap && dream.RealityValue !== "" ? (
                      <div>
                        <Typography color="secondary" variant="h5">
                          RealityValue
                        </Typography>
                        <Typography color="primary" variant="h4">
                          {dream.RealityValue}%
                        </Typography>
                      </div>
                    ) : null}
                    {swap && dream.Character.length > 0 ? (
                      <div>
                        <Typography color="secondary" variant="h5">
                          Characters
                        </Typography>
                        <Typography color="primary" variant="h5">
                          {dream.Character.map((ch) => {
                            return <li>{ch}</li>;
                          })}
                        </Typography>
                      </div>
                    ) : null}
                    {swap && dream.ActiRange !== "" ? (
                      <div>
                        <Typography color="secondary" variant="h5">
                          Emotion Intensity
                        </Typography>
                        <Typography color="primary" variant="h4">
                          {dream.ActiRange}%
                        </Typography>
                      </div>
                    ) : null}
                    {swap && dream.Time !== "" ? (
                      <div>
                        <Typography color="secondary" variant="h5">
                          Time Felt
                        </Typography>
                        <Typography color="primary" variant="h4">
                          {dream.Time}
                        </Typography>
                      </div>
                    ) : null}
                    {swap && dream.Location !== "" ? (
                      <div>
                        <Typography color="secondary" variant="h5">
                          Location
                        </Typography>
                        <Typography color="primary" variant="h4">
                          {dream.location}
                        </Typography>
                      </div>
                    ) : null}
                    <Button
                      onClick={() => {
                        toggleswap();
                      }}
                      variant="contained"
                      color="secondary"
                    >
                      {swap ? (
                        "less"
                      ) : (
                        <img className="downbutton" src={down} />
                      )}
                    </Button>
                    {/* {dream.Name.length > 0 ? (
                      <div>
                        <Typography color="secondary" variant="h4">
                          Name:
                        </Typography>
                        <Typography color="primary" variant="h2">
                          {dream.Name}
                        </Typography>
                      </div>
                    ) : null}
                    {dream.Description.length > 0 ? (
                      <div>
                        <Typography color="secondary" variant="h5">
                          Description:
                        </Typography>
                        <Typography color="primary" variant="h6">
                          {dream.Description}
                        </Typography>
                      </div>
                    ) : null}
                    {dream.Emolist.length > 0 ? (
                      <div>
                        <Typography color="secondary" variant="h5">
                          Emotions Felt
                        </Typography>
                        <Typography color="primary" variant="h4">
                          {dream.Emolist.map((emo) => {
                            return <li>{emo}</li>;
                          })}
                        </Typography>
                      </div>
                    ) : null}
                    {dream.RealityValue !== "" ? (
                      <div>
                        <Typography color="secondary" variant="h5">
                          RealityValue
                        </Typography>
                        <Typography color="primary" variant="h4">
                          {dream.RealityValue}%
                        </Typography>
                      </div>
                    ) : null}
                    {dream.Character.length > 0 ? (
                      <div>
                        <Typography color="secondary" variant="h5">
                          Characters
                        </Typography>
                        <Typography color="primary" variant="h5">
                          {dream.Character.map((ch) => {
                            return <li>{ch}</li>;
                          })}
                        </Typography>
                      </div>
                    ) : null}
                    {dream.ActiRange !== "" ? (
                      <div>
                        <Typography color="secondary" variant="h5">
                          Emotion Intensity
                        </Typography>
                        <Typography color="primary" variant="h4">
                          {dream.ActiRange}%
                        </Typography>
                      </div>
                    ) : null}
                    {dream.Time !== "" ? (
                      <div>
                        <Typography color="secondary" variant="h5">
                          Time Felt
                        </Typography>
                        <Typography color="primary" variant="h4">
                          {dream.Time}
                        </Typography>
                      </div>
                    ) : null}
                    {dream.Location !== "" ? (
                      <div>
                        <Typography color="secondary" variant="h5">
                          Location
                        </Typography>
                        <Typography color="primary" variant="h4">
                          {dream.location}
                        </Typography>
                      </div>
                    ) : null} */}
                  </div>
                </Grid>
              </Grid>
            </li>
          );
        })}
      </ul>
    </div>
  );
};
export default DreamList;
