import React, { useEffect, useState, useContext } from "react";
import { AuthContext } from "../../Auth";
import YourEmoData from "./YourEmoData";
import YourCharacterData from "./YourCharacterData";
import YourTimeData from "./YourTimeData";
import YourEmoIntensity from "./YourEmoIntensity";
import YourRealityValue from "./YourRealityValue";
import Charactercomparison from "../Comparion/Charactercomparison";
import UserEmotion from "../Comparion/Useremotion";
import Timecomp from "../Comparion/Timecomp";
import { Grid, Typography } from "@material-ui/core/";
import firebase from "../firebase";
import "./YourDreamWrapper.css";
function YourDreamWrapper() {
  const [dreams, setdreams] = useState([]);
  const { currentUser } = useContext(AuthContext);
  useEffect(() => {
    const fetchData = async () => {
      const db = firebase.firestore();
      const data = await db
        .collection("Users")
        .doc(currentUser.email)
        .collection("dreams")
        .get();
      setdreams(data.docs.map((doc) => doc.data()));
    };
    fetchData();
  }, []);
  let sour = "";
  if (dreams.length == 0) {
    sour = <Typography></Typography>;
  } else if (dreams.length > 0) {
    sour = (
      <div className="wrappedimg">
        <YourEmoData />
        {sour}
        <Grid container justify="center">
          <Grid item>
            <UserEmotion />
          </Grid>
        </Grid>
        <br />
        <br />
        <br />
        <br />

        <YourCharacterData />

        <Charactercomparison />
        <br />
        <br />
        <br />
        <br />
        <YourTimeData />
        <Timecomp />
        <br />
        <br />
        <br />
        <br />
        <YourEmoIntensity />
        <YourRealityValue />
      </div>
    );
  }
  return <div>{sour}</div>;
}

export default YourDreamWrapper;
