import React from "react";
import { Button, Grid, Typography, Input } from "@material-ui/core";
import "./Location.css";
import Sky from "react-sky";
import cloud from "../Components/images/cloud.png";
function Location(props) {
  return (
    <div className="Location">
      <Grid container justify="center" spacing={3}>
        <Grid item className="locblack">
          <Typography color="secondary" variant="h4">
            Location
          </Typography>
        </Grid>

        <Grid item xs={12}>
          <input
            className="inputclass"
            onChange={(e) => {
              props.setlocation(e.target.value);
            }}
            placeholder="...location"
            variant="contained"
            color="primary"
          />
        </Grid>
      </Grid>
      <Grid container justify="center" spacing={5}>
        <Grid item>
          <Button color="primary" variant="contained" onClick={props.prev}>
            back
          </Button>
        </Grid>
        <Grid item>
          <Button color="primary" variant="contained" onClick={props.next}>
            next
          </Button>
        </Grid>
        <Grid item xs={12}>
          <Typography variant="h3">{props.location}</Typography>
        </Grid>
      </Grid>
    </div>
  );
}

export default Location;
