import React from "react";
import { Button, Typography, Grid } from "@material-ui/core";
import "./LogDreamWeb.css";
function LogDreamWeb(props) {
  return (
    <div>
      {props.dreamlogged === !true ? (
        <div className="butop">
          <Button
            className="panic"
            color="secondary"
            variant="contained"
            onClick={props.logdream}
          >
            Log Dream to Public
          </Button>
        </div>
      ) : (
        <Typography color="secondary">
          Dream Logged to Dream Web! 
        </Typography>
      )}
    </div>
  );
}

export default LogDreamWeb;
