import React, { useState, useEffect, useContext } from "react";
import firebase from "../firebase";
import { AuthContext } from "../../Auth";
import { Typography, Box, Paper, Grid, Divider } from "@material-ui/core";
import { Bar } from "react-chartjs-2";

import "./Useremotion.css";
function Useremotions() {
  const [dreamz, setdreamz] = useState([]);
  const [dreams, setdreams] = useState([]);
  const { currentUser } = useContext(AuthContext);
  const [page, setpage] = useState("bar");
  useEffect(() => {
    const fetchData = async () => {
      const db = firebase.firestore();
      const data = await db.collection("Dream Total").get();
      setdreamz(data.docs.map((doc) => doc.data()));
    };
    fetchData();
  }, []);
  useEffect(() => {
    const fetchData = async () => {
      const db = firebase.firestore();
      const data = await db
        .collection("Users")
        .doc(currentUser.email)
        .collection("dreams")
        .get();
      setdreams(data.docs.map((doc) => doc.data()));
    };
    fetchData();
  }, []);

  const emolisttotal = dreamz.map((dream) => {
    return dream.Emolist;
  });

  const yourlistlist = dreams.map((dream) => {
    return dream.Emolist;
  });
  const countOccurrences = function (arr, val) {
    return arr.reduce((acc, elem) => {
      return val === elem ? acc + 1 : acc;
    }, 0);
  };
  const mergeemototal = [].concat.apply([], emolisttotal);
  const youremototal = [].concat.apply([], yourlistlist);

  //personal occurrence
  const youremofearoccurrence = countOccurrences(youremototal, "Fear");
  const youremoangeroccurrence = countOccurrences(youremototal, "Anger");
  const youremosadnessoccurrence = countOccurrences(youremototal, "Sadness");
  const youremojoyoccurrence = countOccurrences(youremototal, "Joy");
  const youremodisgustoccurrence = countOccurrences(youremototal, "Disgust");
  const youremosurpriseoccurrence = countOccurrences(youremototal, "Surprise");
  const youremotrustoccurrence = countOccurrences(youremototal, "Trust");
  const youremolustoccurrence = countOccurrences(youremototal, "Lust");
  // personal occurrence array
  const youremotionarray = [
    youremofearoccurrence,
    youremoangeroccurrence,
    youremosadnessoccurrence,
    youremojoyoccurrence,
    youremodisgustoccurrence,
    youremosurpriseoccurrence,
    youremotrustoccurrence,
    youremolustoccurrence,
  ];

  //global occurrence
  const globalemofearoccurrence = countOccurrences(mergeemototal, "Fear");
  const globalemoangeroccurrence = countOccurrences(mergeemototal, "Anger");
  const globalemosadnessoccurrence = countOccurrences(mergeemototal, "Sadness");
  const globalemojoyoccurrence = countOccurrences(mergeemototal, "Joy");
  const globalemodisgustoccurrence = countOccurrences(mergeemototal, "Disgust");
  const globalemosurpriseoccurrence = countOccurrences(
    mergeemototal,
    "Surprise"
  );
  const globalemotrustoccurrence = countOccurrences(mergeemototal, "Trust");
  const globalemolustoccurrence = countOccurrences(mergeemototal, "Lust");

  //global occurrence array
  const globalemotionarray = [
    globalemofearoccurrence,
    globalemoangeroccurrence,
    globalemosadnessoccurrence,
    globalemojoyoccurrence,
    globalemodisgustoccurrence,
    globalemosurpriseoccurrence,
    globalemotrustoccurrence,
    globalemolustoccurrence,
  ];
  // add array
  const arrSum = (arr) => arr.reduce((a, b) => a + b, 0);
  //sums
  const globalsum = arrSum(globalemotionarray);
  const yoursum = arrSum(youremotionarray);

  // global averages
  const globalfearaverage = Math.round(
    (globalemofearoccurrence / globalsum) * 100
  );
  const globalangeraverage = Math.round(
    (globalemoangeroccurrence / globalsum) * 100
  );
  const globalemosadnessaverage = Math.round(
    (globalemosadnessoccurrence / globalsum) * 100
  );
  const globaljoyaverage = Math.round(
    (globalemojoyoccurrence / globalsum) * 100
  );
  const globaldisgustaverage = Math.round(
    (globalemodisgustoccurrence / globalsum) * 100
  );
  const globalsurpriseaverage = Math.round(
    (globalemosurpriseoccurrence / globalsum) * 100
  );
  const globaltrustaverage = Math.round(
    (globalemotrustoccurrence / globalsum) * 100
  );

  const globallustaverage = Math.round(
    (globalemolustoccurrence / globalsum) * 100
  );

  //your averages
  const yourfearaverage = Math.round((youremofearoccurrence / yoursum) * 100);
  const yourangeraverage = Math.round((youremoangeroccurrence / yoursum) * 100);
  const yoursadnessaverage = Math.round(
    (youremosadnessoccurrence / yoursum) * 100
  );
  const yourjoyaverage = Math.round((youremojoyoccurrence / yoursum) * 100);
  const yourdisgustaverage = Math.round(
    (youremodisgustoccurrence / yoursum) * 100
  );
  const yoursurpriseaverage = Math.round(
    (youremosurpriseoccurrence / yoursum) * 100
  );
  const yourtrustaverage = Math.round((youremotrustoccurrence / yoursum) * 100);
  const yourlustaverage = Math.round((youremolustoccurrence / yoursum) * 100);

  // difference calculation
  const feardifference = (globalfearaverage - yourfearaverage) * -1;
  const angerdifference = (globalangeraverage - yourangeraverage) * -1;
  const sadnessdifference = (globalemosadnessaverage - yoursadnessaverage) * -1;
  const joydifference = (globaljoyaverage - yourjoyaverage) * -1;
  const disgustdifference = (globaldisgustaverage - yourdisgustaverage) * -1;
  const surprisedifference = (globalsurpriseaverage - yoursurpriseaverage) * -1;
  const trustdifference = (globaltrustaverage - yourtrustaverage) * -1;
  const lustdifference = (globallustaverage - yourlustaverage) * -1;
  const diffarray = [
    feardifference,
    angerdifference,
    sadnessdifference,
    joydifference,
    disgustdifference,
    surprisedifference,
    trustdifference,
    lustdifference,
  ];
  //words
  console.log(yoursurpriseaverage);
  let fearwords = "";
  if (feardifference > 0) {
    fearwords = (
      <Typography color="secondary">
        {Math.abs(feardifference)}% more fear dreams
      </Typography>
    );
  } else if (feardifference == 0) {
    fearwords = (
      <Typography color="secondary">average amount of fear dreams</Typography>
    );
  } else if (feardifference < 0) {
    fearwords = (
      <Typography color="secondary">
        {Math.abs(feardifference)}% less fear dreams
      </Typography>
    );
  }
  let angerwords = "";
  if (angerdifference > 0) {
    angerwords = (
      <Typography color="secondary">
        {Math.abs(angerdifference)}% more anger dreams
      </Typography>
    );
  } else if (angerdifference == 0) {
    angerwords = (
      <Typography color="secondary">average amount of anger dreams</Typography>
    );
  } else if (angerdifference < 0) {
    angerwords = (
      <Typography color="secondary">
        {Math.abs(angerdifference)}% less anger dreams
      </Typography>
    );
  }

  let sadnesswords = "";
  if (sadnessdifference > 0) {
    sadnesswords = (
      <Typography color="secondary">
        {Math.abs(sadnessdifference)}% more sad dreams
      </Typography>
    );
  } else if (sadnessdifference == 0) {
    sadnesswords = (
      <Typography color="secondary">average amount of sad dreams</Typography>
    );
  } else if (sadnessdifference < 0) {
    sadnesswords = (
      <Typography color="secondary">
        {Math.abs(sadnessdifference)}% less sad dreams
      </Typography>
    );
  }

  let joywords = "";
  if (joydifference > 0) {
    joywords = (
      <Typography color="secondary">
        {Math.abs(joydifference)}% more joy dreams
      </Typography>
    );
  } else if (joydifference == 0) {
    joywords = (
      <Typography color="secondary">average amount of joy dreams</Typography>
    );
  } else if (joydifference < 0) {
    joywords = (
      <Typography color="secondary">
        {Math.abs(joydifference)}% less joy dreams
      </Typography>
    );
  }
  let disgustwords = "";
  if (disgustdifference > 0) {
    disgustwords = (
      <Typography color="secondary">
        {Math.abs(disgustdifference)}% more disgust dreams
      </Typography>
    );
  } else if (disgustdifference == 0) {
    disgustwords = (
      <Typography color="secondary">
        average amount of disgust dreams
      </Typography>
    );
  } else if (disgustdifference < 0) {
    disgustwords = (
      <Typography color="secondary">
        {Math.abs(disgustdifference)}% less disgust dreams
      </Typography>
    );
  }
  let surprisewords = "";
  if (surprisedifference > 0) {
    surprisewords = (
      <Typography color="secondary">
        {Math.abs(surprisedifference)}% more surprise dreams
      </Typography>
    );
  } else if (surprisedifference == 0) {
    surprisewords = (
      <Typography color="secondary">
        average amount of surprise dreams
      </Typography>
    );
  } else if (surprisedifference < 0) {
    surprisewords = (
      <Typography color="secondary">
        {Math.abs(surprisedifference)}% less surprise dreams
      </Typography>
    );
  }
  let trustwords = "";
  if (trustdifference > 0) {
    trustwords = (
      <Typography color="secondary">
        {Math.abs(trustdifference)}% more trust dreams
      </Typography>
    );
  } else if (trustdifference == 0) {
    trustwords = (
      <Typography color="secondary">average amount of trust dreams</Typography>
    );
  } else if (trustdifference < 0) {
    trustwords = (
      <Typography color="secondary">
        {Math.abs(trustdifference)}% less trust dreams
      </Typography>
    );
  }
  let lustwords = "";
  if (lustdifference > 0) {
    lustwords = (
      <Typography color="secondary">
        {Math.abs(lustdifference)}% more lust dreams
      </Typography>
    );
  } else if (lustdifference == 0) {
    lustwords = (
      <Typography color="secondary">average amount of lust dreams</Typography>
    );
  } else if (lustdifference < 0) {
    lustwords = (
      <Typography color="secondary">
        {Math.abs(lustdifference)}% less lust dreams
      </Typography>
    );
  }
  const emodata = {
    labels: [
      "fear",
      "Anger",
      "sadness",
      "Joy",
      "Disgust",
      "Surprise",
      "Trust",
      "Lust",
    ],

    datasets: [
      {
        label: "",
        backgroundColor: [
          "gray",
          "red",
          "blue",
          "orange",
          "green",
          "yellow",
          "purple",
          "pink",
        ],
        borderColor: "rgba(255,99,132,1)",
        borderWidth: 0,
        hoverBackgroundColor: "rgba(255,99,132,0.4)",
        hoverBorderColor: "rgba(255,99,132,1)",
        data: diffarray,
      },
    ],
  };
  switch (page) {
    case "words":
      return (
        <div className="wrapper">
          <Typography variant="h6" color="primary">
            Compared to other's emotions <br /> you have:
          </Typography>

          <Grid container justify="center">
            <Grid item>
              <Box borderRadius="50%">
                <Paper elevation="5">
                  <li className="emoli">
                    {fearwords}
                    <Divider />
                    {angerwords}
                    <Divider />
                    {sadnesswords}
                    <Divider />
                    {joywords}
                    <Divider />
                    {disgustwords}
                    <Divider />
                    {surprisewords}
                    <Divider />
                    {trustwords}
                    <Divider />
                    {lustwords}
                  </li>
                </Paper>
              </Box>
            </Grid>
          </Grid>
          <select value={page} onChange={(e) => setpage(e.currentTarget.value)}>
            <option value="words">words</option>
            <option value="bar">Bar</option>
          </select>
        </div>
      );
    case "bar": {
      return (
        <div>
          <Typography color="secondary" variant="h5">
            Compared to Average
          </Typography>
          <Bar data={emodata} height={300} width={400} />

          <select value={page} onChange={(e) => setpage(e.currentTarget.value)}>
            <option value="words">words</option>
            <option value="bar">Bar</option>
          </select>
        </div>
      );
    }
  }
}

export default Useremotions;
