import firebase from "./firebase";
import React, { useEffect, useState, useContext } from "react";
import { Button, Typography, Grid, Card } from "@material-ui/core";

function RealityValue() {
  const [dreams, setdreams] = useState([]);

  useEffect(() => {
    const fetchData = async () => {
      const db = firebase.firestore();
      const data = await db.collection("Dream Total").get();
      setdreams(data.docs.map((doc) => doc.data()));
    };
    fetchData();
  }, []);

  const realityvaluelistunfilter = dreams.map((dream) => {
    if (dream.RealityValue) {
      return dream.RealityValue;
    }
  });
  const realityvaluelist = realityvaluelistunfilter.filter(
    (item) => item !== undefined
  );
  console.log(realityvaluelist);
  const arrAvg = (arr) => arr.reduce((a, b) => a + b, 0) / arr.length;

  return (
    <div>
      <Typography color="primary" variant="h5">
        Reality Average
      </Typography>

      <Typography color="secondary" variant="h5">
        {Math.round(arrAvg(realityvaluelist))}%
      </Typography>
    </div>
  );
}

export default RealityValue;
