import React, { useState, useEffect, useContext } from "react";
import firebase from "../firebase";
import { AuthContext } from "../../Auth";

import { Pie, Line, Bar, Radar, Polar, Area } from "react-chartjs-2";
import { Button, Typography, Grid, Card } from "@material-ui/core";
function CharacterData() {
  const { currentUser } = useContext(AuthContext);
  const [dreams, setdreams] = useState([]);

  useEffect(() => {
    const fetchData = async () => {
      const db = firebase.firestore();
      const data = await db
        .collection("Users")
        .doc(currentUser.email)
        .collection("dreams")
        .get();
      setdreams(data.docs.map((doc) => doc.data()));
    };
    fetchData();
  }, []);

  const realityvaluelistunfilter = dreams.map((dream) => {
    if (dream.RealityValue) {
      return dream.RealityValue;
    }
  });

  const realityvaluelist = realityvaluelistunfilter.filter(
    (item) => item !== undefined
  );
  const arrAvg = (arr) => arr.reduce((a, b) => a + b, 0) / arr.length;
  const long = dreams.length;
  if (long == 0) {
    return null;
  } else
    return (
      <div>
        <Typography color="primary" variant="h5">
          Reality Average
        </Typography>

        <Typography color="secondary" variant="h5">
          {Math.round(arrAvg(realityvaluelist))}%
        </Typography>
      </div>
    );
}

export default CharacterData;
