import React, { useContext } from "react";
import { TextField, Typography, Button, Grid } from "@material-ui/core";
import "./Name.css";
function Name(props) {
  const checkforlength = () => {
    if (props.name.length > 0) {
      return props.submit();
    } else {
      alert("You must name your dream");
    }
  };
  return (
    <div className="NameWrapper">
      <form>
        <Grid container justify="center" spacing={3}>
          <Grid className="blackname" item>
            <Typography color="secondary" variant="h4">
              Name your Dream
            </Typography>
          </Grid>

          <Grid item xs={12}>
            <textarea
              className="textbox"
              value={props.name}
              onChange={(e) => {
                props.setname(e.target.value);
              }}
              color="secondary"
              variant="filled"
              placeholder="Dream Name..."
            />
            <Grid item>
              <Button type="submit"></Button>
            </Grid>
          </Grid>

          <Grid container spacing={1} justify="center">
            <Grid item>
              <Button variant="contained" color="primary" onClick={props.prev}>
                Back
              </Button>
            </Grid>
            <Grid item>
              <Button
                variant="contained"
                color="primary"
                onClick={() => {
                  checkforlength();
                }}
              >
                Dream Summary
              </Button>
            </Grid>
          </Grid>
        </Grid>
      </form>
    </div>
  );
}

export default Name;
