import React, { useState, useEffect, useContext } from "react";
import firebase from "../firebase";
import { AuthContext } from "../../Auth";
import { Pie, Line, Bar, Radar, Polar, Area } from "react-chartjs-2";
import { Button, Typography, Grid, Card } from "@material-ui/core";

function YourEmoData() {
  const { currentUser } = useContext(AuthContext);
  const [dreams, setdreams] = useState([]);
  const [page, setpage] = useState("Pie");
  const changepage = (page) => {
    setpage(page);
  };

  useEffect(() => {
    const fetchData = async () => {
      const db = firebase.firestore();
      const data = await db
        .collection("Users")
        .doc(currentUser.email)
        .collection("dreams")
        .get();
      setdreams(data.docs.map((doc) => doc.data()));
    };
    fetchData();
  }, []);
  const emolistlist = dreams.map((dream) => {
    return dream.Emolist;
  });

  const countOccurrences = function (arr, val) {
    return arr.reduce((acc, elem) => {
      return val === elem ? acc + 1 : acc;
    }, 0);
  };
  const mergedemo = [].concat.apply([], emolistlist);

  const emofearoccurrence = countOccurrences(mergedemo, "Fear");
  const emoangeroccurrence = countOccurrences(mergedemo, "Anger");
  const emosadnessoccurrence = countOccurrences(mergedemo, "Sadness");
  const emojoyoccurrence = countOccurrences(mergedemo, "Joy");
  const emodisgustoccurrence = countOccurrences(mergedemo, "Disgust");
  const emosurpriseoccurrence = countOccurrences(mergedemo, "Surprise");
  const emotrustoccurrence = countOccurrences(mergedemo, "Trust");
  const emolustoccurrence = countOccurrences(mergedemo, "Lust");
  const emotionarray = [
    emofearoccurrence,
    emoangeroccurrence,
    emosadnessoccurrence,
    emojoyoccurrence,
    emodisgustoccurrence,
    emosurpriseoccurrence,
    emotrustoccurrence,
    emolustoccurrence,
  ];

  const arrSum = (arr) => arr.reduce((a, b) => a + b, 0);
  const long = arrSum(emotionarray);
  const emodata = {
    labels: [
      "Fear",
      "Anger",
      "Sadness",
      "Joy",
      "Disgust",
      "Surprise",
      "Trust",
      "Lust",
    ],
    datasets: [
      {
        backgroundColor: [
          "gray",
          "red",
          "blue",
          "orange",
          "green",
          "yellow",
          "purple",
          "pink",
        ],
        borderColor: "rgba(255,99,132,1)",
        borderWidth: 0,
        hoverBackgroundColor: "rgba(255,99,132,0.4)",
        hoverBorderColor: "rgba(255,99,132,1)",
        data: emotionarray,
      },
    ],
  };

  switch (page) {
    case "Line":
      return (
        <div>
          <Typography color="primary" variant="h3">
            Your Emotion{" "}
          </Typography>
          <Line data={emodata} />
          <select value={page} onChange={(e) => setpage(e.currentTarget.value)}>
            <option value="Line">Line</option>
            <option value="Bar">Bar</option>
            <option value="Radar">Radar</option>
            <option value="Area">Area</option>
            <option value="Pie">Pie</option>
          </select>
        </div>
      );

    case "Pie":
      return (
        <div>
          <Typography color="primary" variant="h4">
            Your Emotions
          </Typography>

          <Pie data={emodata} />
          <select value={page} onChange={(e) => setpage(e.currentTarget.value)}>
            <option value="Line">Line</option>
            <option value="Bar">Bar</option>
            <option value="Radar">Radar</option>
            <option value="Area">Area</option>
            <option value="Pie">Pie</option>
          </select>
        </div>
      );

    case "Bar":
      return (
        <div>
          <Typography color="primary" variant="h4">
            Your Emotions
          </Typography>

          <Bar data={emodata} />
          <select value={page} onChange={(e) => setpage(e.currentTarget.value)}>
            <option value="Line">Line</option>
            <option value="Bar">Bar</option>
            <option value="Radar">Radar</option>
            <option value="Area">Area</option>
            <option value="Pie">Pie</option>
          </select>
        </div>
      );
    case "Radar":
      return (
        <div>
          <Typography color="primary" variant="h4">
            Your Emotions
          </Typography>
          <Radar data={emodata} />
          <select value={page} onChange={(e) => setpage(e.currentTarget.value)}>
            <option value="Line">Line</option>
            <option value="Bar">Bar</option>
            <option value="Radar">Radar</option>
            <option value="Area">Area</option>
            <option value="Pie">Pie</option>
          </select>
        </div>
      );
    case "Area":
      return (
        <div>
          <Typography color="primary" variant="h4">
            Your Emotions
          </Typography>
          <Polar data={emodata} />
          <select value={page} onChange={(e) => setpage(e.currentTarget.value)}>
            <option value="Line">Line</option>
            <option value="Bar">Bar</option>
            <option value="Radar">Radar</option>
            <option value="Area">Area</option>
            <option value="Pie">Pie</option>
          </select>
        </div>
      );
    default:
      break;
  }
}

export default YourEmoData;
