import React, { useState, useEffect, useContext } from "react";
import firebase from "../firebase";
import { AuthContext } from "../../Auth";
import { Typography, Box, Divider, Grid, Paper } from "@material-ui/core";
import "./Timecomp.css";
import { Bar } from "react-chartjs-2";

function Timecomp() {
  const [dreamz, setdreamz] = useState([]);
  const [dreams, setdreams] = useState([]);
  const [page, setpage] = useState("bar");

  const { currentUser } = useContext(AuthContext);
  useEffect(() => {
    const fetchData = async () => {
      const db = firebase.firestore();
      const data = await db.collection("Dream Total").get();
      setdreamz(data.docs.map((doc) => doc.data()));
    };
    fetchData();
  }, []);

  useEffect(() => {
    const fetchData = async () => {
      const db = firebase.firestore();
      const data = await db
        .collection("Users")
        .doc(currentUser.email)
        .collection("dreams")
        .get();
      setdreams(data.docs.map((doc) => doc.data()));
    };
    fetchData();
  }, []);
  const yourtimelist = dreamz.map((dream) => {
    return dream.Time;
  });
  const timelist = dreams.map((dream) => {
    return dream.Time;
  });

  const countOccurrences = function (arr, val) {
    return arr.reduce((acc, elem) => {
      return val === elem ? acc + 1 : acc;
    }, 0);
  };
  const mergetimetotal = [].concat.apply([], timelist);
  const mergeyourtime = [].concat.apply([], yourtimelist);
  //global occurance
  const TimeNormaloccurrance = countOccurrences(mergeyourtime, "Normal");
  const TimeVeryLongoccurrance = countOccurrences(mergeyourtime, "Very Long");
  const TimeShortoccurrance = countOccurrences(mergeyourtime, "Short");
  const TimeLongoccurrance = countOccurrences(mergeyourtime, "Long");
  const TimeVeryShortoccurrance = countOccurrences(mergeyourtime, "Very Short");
  const Timelist = [
    TimeNormaloccurrance,
    TimeVeryLongoccurrance,
    TimeShortoccurrance,
    TimeLongoccurrance,
    TimeVeryShortoccurrance,
  ];

  const YourTimeNormaloccurrance = countOccurrences(mergetimetotal, "Normal");
  const YourTimeVeryLongoccurrance = countOccurrences(
    mergetimetotal,
    "Very Long"
  );

  const YourTimeShortoccurrance = countOccurrences(mergetimetotal, "Short");

  const YourTimeLongoccurrance = countOccurrences(mergetimetotal, "Long");
  const YourTimeVeryShortoccurrance = countOccurrences(
    mergetimetotal,
    "Very Short"
  );
  const YourTimelist = [
    YourTimeNormaloccurrance,
    YourTimeVeryLongoccurrance,
    YourTimeShortoccurrance,
    YourTimeLongoccurrance,
    YourTimeVeryShortoccurrance,
  ];

  const arrSum = (arr) => arr.reduce((a, b) => a + b, 0);
  const globalsum = arrSum(Timelist);
  const yoursum = arrSum(YourTimelist);

  //your averages

  const yournormalaverage = Math.round(
    (YourTimeNormaloccurrance / yoursum) * 100
  );
  const yourTimeVeryLongaverage = Math.round(
    (YourTimeVeryLongoccurrance / yoursum) * 100
  );

  const yourveryshortaverage = Math.round(
    (YourTimeVeryShortoccurrance / yoursum) * 100
  );

  const yourlongaverage = Math.round((YourTimeLongoccurrance / yoursum) * 100);
  const yourshortaverage = Math.round(
    (YourTimeShortoccurrance / yoursum) * 100
  );

  // global averages

  const globalnormalaverage = Math.round(
    (TimeNormaloccurrance / globalsum) * 100
  );
  const globalTimeVerylongaverage = Math.round(
    (TimeVeryLongoccurrance / globalsum) * 100
  );
  const globalShortaverage = Math.round(
    (TimeShortoccurrance / globalsum) * 100
  );

  const globalveryshortaverage = Math.round(
    (TimeVeryShortoccurrance / globalsum) * 100
  );
  const globallongaverage = Math.round((TimeLongoccurrance / globalsum) * 100);

  //differences
  const normaldifference = (globalnormalaverage - yournormalaverage) * -1;
  const verylongdifference =
    (globalTimeVerylongaverage - yourTimeVeryLongaverage) * -1;
  const veryshortdifference =
    (globalveryshortaverage - yourveryshortaverage) * -1;
  const longdifference = (globallongaverage - yourlongaverage) * -1;
  const shortdifference = (globalShortaverage - yourshortaverage) * -1;
  const timedifference = [
    verylongdifference,
    longdifference,
    normaldifference,
    shortdifference,
    veryshortdifference,
  ];
  let normalwords = "";
  if (normaldifference > 0) {
    normalwords = (
      <Typography color="secondary">
        {Math.abs(normaldifference)}% more normal dreams
      </Typography>
    );
  } else if (normaldifference == 0) {
    normalwords = (
      <Typography color="secondary">average amount of normal dreams</Typography>
    );
  } else if (normaldifference < 0) {
    normalwords = (
      <Typography color="secondary">
        {Math.abs(normaldifference)}% less normal dreams
      </Typography>
    );
  }
  let verylongwords = "";
  if (verylongdifference > 0) {
    verylongwords = (
      <Typography color="secondary">
        {Math.abs(verylongdifference)}% more very long dreams
      </Typography>
    );
  } else if (verylongdifference == 0) {
    verylongwords = (
      <Typography color="secondary">
        average amount of very long dreams
      </Typography>
    );
  } else if (verylongdifference < 0) {
    verylongwords = (
      <Typography color="secondary">
        {Math.abs(verylongdifference)}% less very long dreams
      </Typography>
    );
  }

  let veryshortwords = "";
  if (veryshortdifference > 0) {
    veryshortwords = (
      <Typography color="secondary">
        {Math.abs(veryshortdifference)}% more very short dreams
      </Typography>
    );
  } else if (veryshortdifference == 0) {
    veryshortwords = (
      <Typography color="secondary">
        average amount of very short dreams
      </Typography>
    );
  } else if (veryshortdifference < 0) {
    veryshortwords = (
      <Typography color="secondary">
        {Math.abs(veryshortdifference)}% less very short dreams
      </Typography>
    );
  }
  let longwords = "";
  if (longdifference > 0) {
    longwords = (
      <Typography color="secondary">
        {Math.abs(longdifference)}% more very long dreams
      </Typography>
    );
  } else if (longdifference == 0) {
    longwords = (
      <Typography color="secondary">
        average amount of very long dreams
      </Typography>
    );
  } else if (longdifference < 0) {
    longwords = (
      <Typography color="secondary">
        {Math.abs(longdifference)}% less very long dreams
      </Typography>
    );
  }
  let shortwords = "";
  if (shortdifference > 0) {
    shortwords = (
      <Typography color="secondary">
        {Math.abs(shortdifference)}% more short dreams
      </Typography>
    );
  } else if (shortdifference == 0) {
    shortwords = (
      <Typography color="secondary">average amount of short dreams</Typography>
    );
  } else if (shortdifference < 0) {
    shortwords = (
      <Typography color="secondary">
        {Math.abs(shortdifference)}% less short dreams
      </Typography>
    );
  }
  const timedata = {
    labels: ["Very Long", "Long", "Normal", "Short", " Very Short"],
    datasets: [
      {
        label: "",
        backgroundColor: [
          "red",
          "blue",
          "green",
          "white",
          "orange",
          "purple",
          "yellow",
          "black",
        ],
        borderColor: "rgba(255,99,132,1)",
        borderWidth: 0,
        hoverBackgroundColor: "rgba(255,99,132,0.4)",
        hoverBorderColor: "rgba(255,99,132,1)",
        data: timedifference,
      },
    ],
  };

  switch (page) {
    case "words": {
      return (
        <div>
          <Typography variant="h6" color="primary">
            Compared to other's Time <br /> you have:
          </Typography>
          <Grid container justify="center">
            <Grid item>
              <Box borderRadius="50%">
                <Paper elevation="5">
                  <li className="timedata">
                    {normalwords}
                    <Divider />
                    {verylongwords}
                    <Divider />
                    {veryshortwords}
                    <Divider />
                    {longwords}
                    <Divider />
                    {shortwords}
                  </li>
                </Paper>
              </Box>
            </Grid>
          </Grid>
          <select value={page} onChange={(e) => setpage(e.currentTarget.value)}>
            <option value="words">words</option>
            <option value="bar">Bar</option>
          </select>
        </div>
      );
    }
    case "bar": {
      return (
        <div>
          <Typography color="secondary" variant="h5">
            Compared to Average
          </Typography>
          <Bar data={timedata} />

          <select value={page} onChange={(e) => setpage(e.currentTarget.value)}>
            <option value="words">words</option>
            <option value="bar">Bar</option>
          </select>
        </div>
      );
    }
  }
}

export default Timecomp;
