import React from "react";
import { Button, Typography, Grid, Slider, Paper } from "@material-ui/core";
import "./Reality.css";
function Reality(props) {
  return (
    <div className="RealityValue">
      <Grid container justify="center">
        <Grid item className="blackreality">
          <Typography color="secondary" variant="h4">
            Reality
          </Typography>
        </Grid>
      </Grid>
      <br />

      <Grid container justify="center" spacing={5}>
        <Grid item xs={12}>
          <Grid container justify="center">
            <Grid className="blackreality" item>
              <Typography color="secondary" variant="h2">
                {props.RealityValue}
                {props.RealityValue === "" ? null : "%"}
              </Typography>
            </Grid>
          </Grid>
          <Grid item xs={12}></Grid>
        </Grid>
        <Grid className="sides" item xs={3}>
          <Typography color="secondary" variant="h5">
            Not Real
          </Typography>
        </Grid>
        <Grid item xs={4}>
          <div className="blackrealityslider">
            <Slider
              aria-labelledby="discrete-slider-small-steps"
              arial-label="so does it work"
              defaultValue={50}
              valueLabelDisplay="auto"
              step={1}
       
              onChange={(e, value) => props.setRealityValue(value)}
            />
          </div>
        </Grid>
        <Grid className="sides" item xs={3}>
          <Typography color="secondary" variant="h5">
            Very Real
          </Typography>
        </Grid>
      </Grid>

      <Grid container spacing={5} justify="center">
        <Grid item>
          <Button color="primary" variant="contained" onClick={props.prev}>
            back
          </Button>
        </Grid>
        <Grid item>
          <Button variant="contained" color="primary" onClick={props.next}>
            next
          </Button>
        </Grid>
      </Grid>
    </div>
  );
}

export default Reality;
