import React, { useState } from "react";
import "./Emo.css";
import Check from "@material-ui/icons/Check";
import Sky from "react-sky";
import { Button, Typography, Grid, Card, ListItem } from "@material-ui/core";
import cloud from "./images/cloud.png";
function Emo(props) {
  let fearcolor = "secondary";

  return (
    <div className="emowrapper">
      <Grid container justify="center" spacing={1}>
        <Grid item className="feelblack">
          <Typography color="secondary" align="center" variant="h4">
            Feelings Had
          </Typography>
        </Grid>

        <Grid item xs={12}>
          {props.emomore.objects.map((element, index) => (
            <Grid key={index} container spacing={3} justify="center">
              <Grid item>
                <Button
                  color={props.togglecolor(index)}
                  key={index}
                  variant="contained"
                  onClick={() => props.emotoggleActive(element, index)}
                >
                  {element.toggled ? <Check /> : null}
                  {element.id}
                </Button>
              </Grid>
            </Grid>
          ))}
        </Grid>
      </Grid>
      <br />
      <Grid container spacing={2} justify="center">
        <Grid item>
          <Button variant="contained" color="primary" onClick={props.prev}>
            Back
          </Button>
        </Grid>
        <Grid item>
          <Button variant="contained" color="primary" onClick={props.nextpage}>
            Next
          </Button>
        </Grid>{" "}
      </Grid>
    </div>
  );
}

export default Emo;
