import React from "react";
import { Button, Typography, Grid, Paper } from "@material-ui/core";
import LogDreamWeb from "./LogDreamWeb";
import "./Report.css";
function Report(props) {
  return (
    <div className="report">
      <ul>
        <Grid container justify="center" spacing={3}>
          <div className="backdrop">
            <Grid item xs={12}>
              {props.name.length > 0 ? (
                <Typography color="secondary" variant="h5">
                  Name:
                </Typography>
              ) : null}
              <Typography color="primary" variant="h4">
                {props.name}
              </Typography>
              {props.Description.length > 0 ? (
                <Typography color="secondary" variant="h5">
                  Description:
                </Typography>
              ) : null}
              <Typography color="primary" variant="h6">
                {props.Description}
              </Typography>
              {props.Emolist.length > 0 ? (
                <Typography color="secondary" variant="h5">
                  Emotions:
                  <Typography>{props.Emolist}</Typography>
                </Typography>
              ) : null}

              {/* {props.Emolist.map((emo) => {
                return (
                  <Typography color="primary" variant="h4">
                    {emo}
                  </Typography>
                );
              })} */}

              {props.ActiRange === "" ? null : (
                <Typography color="primary" variant="4">
                  <Typography variant="h6" color="secondary">
                    Emotion Strength:
                  </Typography>
                  <Typography color="primary" variant="h4">
                    {props.ActiRange}
                    {"%"}
                  </Typography>
                </Typography>
              )}
              {props.Reality === "" ? null : (
                <Typography color="primary" variant="4">
                  <Typography variant="h6" color="secondary">
                    Reality Meter:
                  </Typography>
                  <Typography color="primary" variant="h4">
                    {props.Reality} {"%"}
                  </Typography>
                </Typography>
              )}
              {props.Time.length === 0 ? null : (
                <>
                  <Typography color="secondary" variant="h6">
                    Time Felt:
                  </Typography>

                  <Typography color="primary" variant="h4">
                    {props.Time}
                  </Typography>
                </>
              )}
              {props.location.length === 0 ? null : (
                <Typography color="secondary" variant="h6">
                  Location:
                </Typography>
              )}
              <Typography color="primary" variant="h4">
                {props.location === "Both"
                  ? `Both Inside and Outside`
                  : props.location}
              </Typography>

              {props.character.length === 0 ? null : (
                <Typography color="secondary" variant="h6">
                  Characters
                </Typography>
              )}
              {props.character.map((peep) => {
                return (
                  <Typography color="primary" variant="h4">
                    {peep}
                  </Typography>
                );
              })}

              <Grid container justify="center" spacing={2}>
                <Grid item xs={12}>
                  <LogDreamWeb
                    logdream={props.logdream}
                    dreamlogged={props.dreamlogged}
                  />
                </Grid>
                <Grid item>
                  <Button
                    onClick={() => props.yourdata()}
                    variant="contained"
                    color="primary"
                  >
                    Your Dream Data
                  </Button>
                </Grid>
                <Grid item xs={12}>
                  <Button
                    color="secondary"
                    variant="contained"
                    onClick={() => props.yourdreams()}
                  >
                    Your Dreams
                  </Button>
                </Grid>
              </Grid>
            </Grid>
          </div>
        </Grid>
      </ul>
    </div>
  );
}

export default Report;
