import React, { useState, useCallback } from "react";
import { withRouter } from "react-router";
import { Redirect, Link } from "react-router-dom";
import Emodata from "./Emodata";
import { auth } from "./firebase";
import "./Signup.css";
import { Typography, Button, Grid, Paper } from "@material-ui/core";
import cloud from "./images/cloud.png";

import Sky from "react-sky";

const SignUp = ({ history, props }) => {
  const [displayname, setdisplayname] = useState("");
  const handleSignUp = useCallback(
    async (event) => {
      event.preventDefault();
      const { email, password } = event.target.elements;
      try {
        await auth.createUserWithEmailAndPassword(email.value, password.value);
        history.push("/DreamBuddy");
        auth.currentUser.updateProfile({
          displayName: displayname,
        });
        console.log(auth.currentUser.email);
      } catch (error) {
        alert(error);
      }
    },
    [history]
  );

  return (
    <div className="signup">
      <Grid container justify="center">
        <div className="blackoutbuddy">
          <Grid item xs={12}>
            <Typography color="primary" variant="h3">
              Dreamer Buddy
            </Typography>
          </Grid>
        </div>
      </Grid>
      <Grid container justify="center">
        <div className="collectblackout">
          <Grid item xs={12}>
            <Typography color="secondary" variant="h5">
              Collecting the Collective <br /> Unconcious
            </Typography>
          </Grid>
        </div>
      </Grid>
      <br />

      <Grid container justify="center">
        <Grid item>
          <div className="collect">
            <Typography color="primary" variant="h5">
              Sign up
            </Typography>
            <form onSubmit={handleSignUp}>
              <Typography color="secondary">Email</Typography>

              <input name="email" type="email" placeholder="Email" />
              <br />

              <Typography color="secondary">Password</Typography>

              <input name="password" type="password" placeholder="Password" />

              <div className="split">
                <br />
                <Button variant="contained" color="primary" type="submit">
                  Sign Up
                </Button>
              </div>
            </form>
            <Typography variant="h5" color="secondary">
              or
            </Typography>
            <Grid container justify="center">
              <Grid item xs={12}>
                <Button color="secondary" variant="contained">
                  <Link className="link" to="/Login">
                    <Typography color="primary">Login</Typography>
                  </Link>
                </Button>
              </Grid>
              <Grid item xs={12}>
                <br />
                <Link className="link" to="/LearnMore">
                  <Button color="secondary" variant="contained">
                    Learn More
                  </Button>
                </Link>
              </Grid>
            </Grid>
          </div>
        </Grid>
      </Grid>
    </div>
  );
};

export default withRouter(SignUp);
