import React from "react";
import { auth } from "../firebase";
import { Button } from "@material-ui/core";
import "./Begin.css";
function Begin(props) {
  return (
    <div className="start">
      <Button
        color="primary"
        size="large"
        variant="contained"
        onClick={props.start}
      >
        Report Dream
      </Button>
    </div>
  );
}

export default Begin;
